<template>
  <v-card flat>
    <v-layout row wrap>
      <v-flex xs12 md6 offset-md3>
        <v-text-field label="Rechercher" v-model="search" />
      </v-flex>
      <v-flex xs12>
        <v-data-table
          :headers="headers"
          :items="items"
          item-key="ID"
          :loading="loading"
          :search="search"
          class="elevation-1"
        >
          <template slot="items" slot-scope="props">
            <td class="text-xs-left">{{ props.item.Name }}</td>
            <td class="text-xs-left">{{ props.item.Date | date }}</td>
            <td class="pr-0 pl-0">
              <v-tooltip left>
                <template v-slot:activator="{ on }">
                  <v-btn
                    color="primary"
                    small
                    flat
                    icon
                    class="pa-0"
                    @click="modify(props.item)"
                    v-on="on"
                  >
                    <v-icon>create</v-icon>
                  </v-btn>
                </template>
                Modifier
              </v-tooltip>
            </td>
            <td class="pr-0 pl-0">
              <v-tooltip left>
                <template v-slot:activator="{ on }">
                  <v-btn
                    color="primary"
                    small
                    icon
                    flat
                    class="pa-0"
                    v-on="on"
                    @click="remove(props.item)"
                  >
                    <v-icon>delete</v-icon>
                  </v-btn>
                </template>
                Supprimer
              </v-tooltip>
            </td>
          </template>
        </v-data-table>
      </v-flex>
    </v-layout>
    <v-divider />
    <v-card-actions>
      <v-spacer />
      <v-btn color="primary" small flat @click="add">Créer</v-btn>
    </v-card-actions>

    <v-dialog v-model="dialog" persistent :overlay="false" max-width="500px">
      <v-card>
        <v-card-title class="primary white--text headline">
          {{ dlgTitle }}
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout row wrap="">
              <v-flex xs12>
                <v-text-field
                  label="Nom"
                  v-model="item.Name"
                  @keyup.esc="dialog = false"
                  @keyup.enter="save"
                />
              </v-flex>
              <v-flex xs12>
                <v-menu
                  ref="menu"
                  v-model="menu"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  :return-value.sync="item.Date"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
                  min-width="290px"
                >
                  <template v-slot:activator="{ on }">
                    <v-text-field
                      v-model="formattedDate"
                      label="Date de la commission"
                      prepend-icon="event"
                      readonly
                      v-on="on"
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    v-model="item.Date"
                    no-title
                    scrollable
                    locale="fr-FR"
                  >
                    <v-spacer></v-spacer>
                    <v-btn flat color="primary" @click="menu = false">
                      Annuler
                    </v-btn>
                    <v-btn
                      flat
                      color="primary"
                      @click="$refs.menu.save(item.Date)"
                    >
                      OK
                    </v-btn>
                  </v-date-picker>
                </v-menu>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" small flat @click="dialog = false">
            Annuler
          </v-btn>
          <v-btn color="primary" small flat @click="save" :disabled="disabled">
            {{ dlgBtn }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <delete-dialog
      v-model="delDlg"
      sentence="Supprimer la commission définitivement ?"
      @confirm="delConfirm"
    />
  </v-card>
</template>

<script>
import * as types from '../store/types.js'
import { formatNullDate } from '../date.js'
import DeleteDialog from './DeleteDialog'
export default {
  name: 'Commission',
  components: { DeleteDialog },
  data() {
    return {
      search: '',
      headers: [
        { text: 'Nom', value: 'Name' },
        { text: 'Date', value: 'Date' },
        { text: '', value: '', sortable: false, width: '1%' },
        { text: '', value: '', sortable: false, width: '1%' }
      ],
      dialog: false,
      delDlg: false,
      item: { Name: '', Date: null },
      dlgTitle: 'Nouvelle commission',
      dlgBtn: 'Créer',
      menu: null
    }
  },
  methods: {
    remove(item) {
      this.item.ID = item.ID
      this.delDlg = true
    },
    modify(item) {
      this.item.ID = item.ID
      this.item.Name = item.Name
      this.item.Date = item.Date
      this.dlgTitle = `Modifier la commission`
      this.dlgBtn = 'Modifier'
      this.dialog = true
    },
    add(item) {
      this.item.ID = null
      this.item.Name = ''
      this.item.Date = null
      this.dlgTitle = 'Nouvelle commission'
      this.dlgBtn = 'Créer'
      this.dialog = true
    },
    async save() {
      if (!this.disabled) {
        let date = null
        if (this.item.Date) {
          let d = new Date(this.item.Date)
          date = d.toISOString()
        }
        let dispatch =
          this.dlgBtn === 'Créer'
            ? types.CREATE_COMMISSION
            : types.UPDATE_COMMISSION
        await this.$store.dispatch(dispatch, {
          ID: this.item.ID,
          Name: this.item.Name,
          Date: date
        })
        this.dialog = false
      }
    },
    async delConfirm() {
      await this.$store.dispatch(types.DEL_COMMISSION, this.item.ID)
      this.delDlg = false
    }
  },
  computed: {
    items() {
      return this.$store.state.settings.commissionsList
    },
    loading() {
      return this.$store.state.loading.loading !== 0
    },
    disabled() {
      return this.item.Name === null || this.item.Name === ''
    },
    formattedDate() {
      return formatNullDate(this.item.Date)
    }
  }
}
</script>
