<template>
  <v-layout row wrap>
    <v-flex xs12 pa-2 sm6 offset-sm3>
      <v-text-field
        label="Année"
        v-debounce:500ms="yearChanged"
        :rules="[yearRule]"
      ></v-text-field>
    </v-flex>
    <v-flex xs12 pr-2 pl-2>
      <v-data-table
        :headers="headers"
        :items="items"
        class="elevation-1"
        item-key="ID"
        :loading="loading"
      >
        <template slot="items" slot-scope="props">
          <td class="text-xs-right">{{ props.item.Index }}</td>
          <td class="text-xs-right">{{ props.item.SectorName }}</td>
          <td class="text-xs-right">{{ props.item.Ratio | percentage }}</td>
        </template>
      </v-data-table>
    </v-flex>
    <v-flex xs12 pt-2>
      <v-divider />
    </v-flex>
    <v-flex xs12 class="text-xs-right">
      <v-btn
        flat
        color="primary"
        :disabled="disabled"
        @click="save"
        v-if="isAdmin"
        >Sauver</v-btn
      >
    </v-flex>
  </v-layout>
</template>

<script>
import * as types from '../store/types'
const yearTest = x => /^20\d{2}$/.test(x)
export default {
  name: 'Ratios',
  data() {
    return {
      headers: [
        { text: 'Année n+', value: 'Index', align: 'center' },
        { text: 'Secteur', value: 'SectorName', align: 'center' },
        { text: 'Taux', value: 'Ratio', align: 'center' }
      ],
      disabled: true,
      year: 0
    }
  },
  methods: {
    yearRule: txt => (yearTest(txt) ? true : 'Année de type 2000 attendue'),
    yearChanged(y) {
      if (yearTest(y)) {
        this.year = Number(y)
        this.$store.dispatch(types.GET_YEAR_RATIOS, { Year: this.year })
        this.disabled = false
      }
    },
    save() {
      this.$store.dispatch(types.SAVE_YEAR_RATIOS, {
        Year: this.year,
        Ratios: this.items
      })
    }
  },
  computed: {
    items() {
      return this.$store.state.ratios.ratiosList.map((r, i) => ({
        ...r,
        ID: i
      }))
    },
    loading() {
      return this.$store.state.loading.loading !== 0
    },
    isAdmin() {
      return this.$store.getters.isAdmin
    }
  }
}
</script>
