<template>
  <v-layout row wrap class="pa-2">
    <v-flex xs12 sm6 offset-sm3>
      <v-select
        :items="years"
        v-model="ratioYear"
        label="Engagements de l'année"
        class
      />
    </v-flex>
    <v-flex xs12>
      <v-data-table
        :headers="headers"
        :items="items"
        class="elevation-1"
        item-key="ActionID"
        :loading="loading"
      >
        <template slot="items" slot-scope="props">
          <tr>
            <td>{{ props.item.ActionName }}</td>
            <td class="text-xs-right">{{ props.item.Y0 | currency }}</td>
            <td class="text-xs-right">{{ props.item.Y1 | currency }}</td>
            <td class="text-xs-right">{{ props.item.Y2 | currency }}</td>
            <td class="text-xs-right">{{ props.item.Y3 | currency }}</td>
            <td class="text-xs-right">{{ props.item.Y4 | currency }}</td>
          </tr>
        </template>
        <template slot="footer">
          <tr class="body-2">
            <td>Total</td>
            <td class="text-xs-right">{{ TY0 | currency }}</td>
            <td class="text-xs-right">{{ TY1 | currency }}</td>
            <td class="text-xs-right">{{ TY2 | currency }}</td>
            <td class="text-xs-right">{{ TY3 | currency }}</td>
            <td class="text-xs-right">{{ TY4 | currency }}</td>
          </tr>
        </template>
      </v-data-table>
    </v-flex>
  </v-layout>
</template>

<script>
import * as types from '../store/types'
export default {
  name: 'PaymentForecasts',
  data() {
    return {
      headers: [
        { text: 'Action budgétaire', value: 'ActionName' },
        { text: 'Y0', value: 'Y0' },
        { text: 'Y1', value: 'Y1' },
        { text: 'Y2', value: 'Y2' },
        { text: 'Y3', value: 'Y3' },
        { text: 'Y4', value: 'Y4' }
      ],
      ratioYear: null,
      TY0: 0,
      TY1: 0,
      TY2: 0,
      TY3: 0,
      TY4: 0
    }
  },
  computed: {
    items() {
      return this.$store.state.pmtForecasts.pmtForecastsList
    },
    years() {
      return this.$store.state.ratios.ratiosYears
    },
    loading() {
      return this.$store.state.loading.loading !== 0
    }
  },
  created() {
    const now = new Date().getFullYear()
    for (let i = 1; i < 6; i++) {
      this.headers[i].text = String(now + i - 1)
    }
    this.$store.dispatch(types.GET_RATIOS_YEARS)
  },
  watch: {
    async ratioYear(val) {
      if (val) {
        await this.$store.dispatch(types.GET_PMT_FORECASTS, { Year: val })
        this.TY0 = 0
        this.TY1 = 0
        this.TY2 = 0
        this.TY3 = 0
        this.TY4 = 0
        this.items.forEach(i => {
          this.TY0 += i.Y0
          this.TY1 += i.Y1
          this.TY2 += i.Y2
          this.TY3 += i.Y3
          this.TY4 += i.Y4
        })
      }
    }
  }
}
</script>
