<template>
  <div>
    <v-btn color="primary" small flat @click="pickFile">{{ name }}</v-btn>
    <input
      type="file"
      style="display: none"
      ref="image"
      accept=".xlsx"
      @change="onFilePicked"
    />
  </div>
</template>

<script>
export default {
  name: 'UploadButton',
  props: {
    name: { type: String, default: 'Télécharger' }
  },
  methods: {
    pickFile() {
      this.$refs.image.click()
    },
    onFilePicked(e) {
      const files = e.target.files
      if (files[0] !== undefined) {
        this.$emit('fileChosen', files[0])
      }
    }
  }
}
</script>
