<template>
  <v-card flat>
    <v-layout row wrap>
      <v-flex xs12 md6 offset-md3>
        <v-text-field label="Rechercher" v-model="search" />
      </v-flex>
      <v-flex xs12>
        <v-data-table
          :headers="headers"
          :items="items"
          item-key="ID"
          :loading="loading"
          :search="search"
          class="elevation-1"
        >
          <template slot="items" slot-scope="props">
            <td class="text-xs-left">{{ props.item.Code }}</td>
            <td class="text-xs-left">{{ props.item.Name }}</td>
            <td class="pr-0 pl-0">
              <v-tooltip left>
                <template v-slot:activator="{ on }">
                  <v-btn
                    color="primary"
                    small
                    flat
                    icon
                    class="pa-0"
                    @click="modify(props.item)"
                    v-on="on"
                  >
                    <v-icon>create</v-icon>
                  </v-btn>
                </template>
                Modifier
              </v-tooltip>
            </td>
            <td class="pr-0 pl-0">
              <v-tooltip left>
                <template v-slot:activator="{ on }">
                  <v-btn
                    color="primary"
                    small
                    icon
                    flat
                    class="pa-0"
                    v-on="on"
                    @click="remove(props.item)"
                  >
                    <v-icon>delete</v-icon>
                  </v-btn>
                </template>
                Supprimer
              </v-tooltip>
            </td>
          </template>
        </v-data-table>
      </v-flex>
    </v-layout>
    <v-divider />
    <v-card-actions>
      <v-spacer />
      <upload-button @fileChosen="upload($event)" />
      <v-btn color="primary" small flat @click="add">Créer</v-btn>
    </v-card-actions>

    <v-dialog v-model="dialog" persistent :overlay="false" max-width="500px">
      <v-card>
        <v-card-title class="primary white--text headline">
          {{ dlgTitle }}
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout row wrap="">
              <v-flex xs12>
                <v-text-field
                  label="Code (INSEE)"
                  v-model="item.Code"
                  @keyup.esc="dialog = false"
                  @keyup.enter="save"
                />
              </v-flex>
              <v-flex xs12>
                <v-text-field
                  label="Nom"
                  v-model="item.Name"
                  @keyup.esc="dialog = false"
                  @keyup.enter="save"
                />
              </v-flex>
            </v-layout>
          </v-container>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" small flat @click="dialog = false">
            Annuler
          </v-btn>
          <v-btn color="primary" small flat @click="save" :disabled="disabled">
            {{ dlgBtn }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <delete-dialog
      v-model="delDlg"
      sentence="Supprimer l'intercommunalité définitivement ?"
      @confirm="delConfirm"
    />
  </v-card>
</template>

<script>
import * as types from '../store/types.js'
import UploadButton from './UploadButton.vue'
import DeleteDialog from './DeleteDialog'
export default {
  name: 'Communities',
  components: { UploadButton, DeleteDialog },
  data() {
    return {
      search: '',
      headers: [
        { text: 'Code', value: 'Code' },
        { text: 'Nom', value: 'Name' },
        { text: '', value: '', sortable: false, width: '1%' },
        { text: '', value: '', sortable: false, width: '1%' }
      ],
      dialog: false,
      delDlg: false,
      item: { Code: null, Name: null },
      dlgTitle: 'Nouvelle intercommunalité',
      dlgBtn: 'Créer',
      menu: null
    }
  },
  methods: {
    remove(item) {
      this.item.ID = item.ID
      this.delDlg = true
    },
    modify(item) {
      this.item.ID = item.ID
      this.item.Name = item.Name
      this.item.Code = item.Code
      this.dlgTitle = `Modifier l'intercommunalité`
      this.dlgBtn = 'Modifier'
      this.dialog = true
    },
    add(item) {
      this.item.ID = null
      this.item.Name = null
      this.item.Code = null
      this.dlgTitle = 'Nouvelle intercommunalité'
      this.dlgBtn = 'Créer'
      this.dialog = true
    },
    async save() {
      if (!this.disabled) {
        let dispatch =
          this.dlgBtn === 'Créer'
            ? types.CREATE_COMMUNITY
            : types.UPDATE_COMMUNITY
        await this.$store.dispatch(dispatch, this.item)
        this.dialog = false
      }
    },
    async delConfirm() {
      await this.$store.dispatch(types.DEL_COMMUNITY, this.item.ID)
      this.delDlg = false
    },
    upload(file) {
      this.$store.dispatch(types.UPLOAD_COMMUNITIES, file)
    }
  },
  computed: {
    items() {
      return this.$store.state.communities.communitiesList
    },
    loading() {
      return this.$store.state.loading.loading !== 0
    },
    disabled() {
      return (
        this.item.Name === null ||
        this.item.Name === '' ||
        this.item.Code === null ||
        this.item.code === ''
      )
    }
  }
}
</script>
