<template>
  <v-card flat>
    <v-layout row wrap>
      <v-flex xs12 sm6 pa-2>
        <v-text-field
          label="Rechercher (engagement, bénéficiaire, action)"
          v-debounce:500ms="newSearch"
        />
      </v-flex>
      <v-flex xs12 sm6 pa-2>
        <v-text-field
          label="À partir de l'année"
          v-debounce:500ms="newYear"
          :rules="[yearRule]"
        />
      </v-flex>
      <v-flex xs12>
        <v-data-table
          :headers="headers"
          :items="items"
          item-key="ID"
          :loading="loading"
          :rows-per-page-items="[10]"
          :total-items="itemsCount"
          :pagination.sync="pagination"
          hide-actions
          class="elevation-1"
        >
          <template slot="items" slot-scope="props">
            <td class="text-xs-left">{{ props.item.CreationDate | date }}</td>
            <td class="text-xs-left">{{ props.item.Number }}</td>
            <td class="text-xs-right">{{ props.item.Value | currency }}</td>
            <td class="text-xs-left">{{ props.item.CommitmentDate | date }}</td>
            <td class="text-xs-left">{{ props.item.CommitmentName }}</td>
            <td class="text-xs-right">
              {{ props.item.CommitmentValue | currency }}
            </td>
            <td class="text-xs-left">{{ props.item.Beneficiary }}</td>
            <td class="text-xs-left">{{ props.item.Budget }}</td>
          </template>
        </v-data-table>
        <div class="text-xs-center pa-2">
          <v-pagination
            v-model="pagination.page"
            :length="pages"
            :total-visible="7"
            @input="getPayments"
          />
        </div>
      </v-flex>
    </v-layout>
    <v-divider />
    <v-card-actions>
      <v-spacer />
      <v-btn flat small @click="excelExport()" color="primary"
        >Export Excel</v-btn
      >
      <upload-button @fileChosen="upload($event)" />
    </v-card-actions>
  </v-card>
</template>

<script>
import * as types from '../store/types.js'
import UploadButton from './UploadButton.vue'
export default {
  name: 'Payments',
  components: { UploadButton },
  data() {
    return {
      search: '',
      year: 0,
      headers: [
        { text: 'Date pmt', value: 'CreationDate' },
        { text: 'Année/num pmt', value: 'Number' },
        { text: 'Montant pmt', value: 'Value' },
        { text: 'Date egt', value: 'CommitmentDate' },
        { text: 'Nom egt', value: 'CommitmentName' },
        { text: 'Montant egt', value: 'CommitmentValue' },
        { text: 'Bénéficiaire', value: 'Beneficiary' },
        { text: 'Budget', value: 'Budget' }
      ],
      pagination: {}
    }
  },
  methods: {
    upload(file) {
      this.$store.dispatch(types.UPLOAD_PAYMENTS, file)
    },
    newSearch(val) {
      this.search = val
      this.getPayments()
    },
    newYear(val) {
      if (/^20\d{2}$/.test(val)) {
        this.year = Number(val)
        this.getPayments()
      }
      if (val === '' || !val) {
        this.year = 0
        this.getPayments()
      }
    },
    yearRule(val) {
      return /^20\d{2}$/.test(val) || val === '' || 'Année attendue'
    },
    async getPayments() {
      await this.$store.dispatch(types.GET_PAYMENTS, {
        Year: this.year,
        Search: this.search,
        Page: this.pagination.page
      })
    },
    excelExport() {
      this.$store.dispatch(types.EXPORT_PAYMENTS, {
        Year: this.year,
        Search: this.search
      })
    }
  },
  computed: {
    items() {
      return this.$store.state.payments.paymentsList.map(p => ({
        CreationDate: p.CreationDate,
        Number: '[' + p.Year + '] ' + p.Number,
        Value: p.Value,
        CommitmentDate: p.CommitmentDate,
        CommitmentValue: p.CommitmentValue,
        CommitmentName: p.CommitmentName,
        Beneficiary: p.Beneficiary,
        Budget: '[' + p.Sector + '] ' + p.ActionName
      }))
    },
    loading() {
      return this.$store.state.loading.loading !== 0
    },
    itemsCount() {
      return this.$store.state.payments.paymentsItemsCount
    },
    pages() {
      if (this.pagination.rowsPerPage == null || this.itemsCount == null)
        return 0

      return Math.ceil(this.itemsCount / this.pagination.rowsPerPage)
    }
  }
}
</script>
