<template>
  <v-card flat>
    <v-layout row wrap>
      <v-flex xs12 sm6 pa-2>
        <v-text-field
          label="Rechercher (nom, code, bénéficiaire)"
          v-debounce:500ms="newSearch"
        />
      </v-flex>
      <v-flex xs12 sm6 pa-2>
        <v-text-field
          label="À partir de l'année"
          v-debounce:500ms="newYear"
          :rules="[yearRule]"
        />
      </v-flex>
      <v-flex xs12>
        <v-data-table
          :headers="headers"
          :items="items"
          item-key="ID"
          :loading="loading"
          :rows-per-page-items="[10]"
          :total-items="itemsCount"
          :pagination.sync="pagination"
          select-all
          v-model="selected"
          hide-actions
          class="elevation-1"
        >
          <template v-slot:headers="props">
            <tr>
              <th>
                <v-checkbox
                  :input-value="props.all"
                  :indeterminate="props.indeterminate"
                  color="primary"
                  hide-details
                  :disabled="!allowLink"
                  @click.stop="toggleAll"
                />
              </th>
              <th v-for="header in props.headers" :key="header.text">
                {{ header.text }}
              </th>
            </tr>
          </template>
          <template v-slot:items="props">
            <td>
              <v-checkbox
                v-model="props.selected"
                color="primary"
                hide-details
                :disabled="!allowLink"
              />
            </td>
            <td class="text-xs-left">{{ props.item.CreationDate | date }}</td>
            <td class="text-xs-left">{{ props.item.Budget }}</td>
            <td class="text-xs-left">{{ props.item.FullCode }}</td>
            <td class="text-xs-left">{{ props.item.Name }}</td>
            <td class="text-xs-left">{{ props.item.Beneficiary }}</td>
            <td class="text-xs-right">{{ props.item.Value | currency }}</td>
            <td class="text-xs-center">
              <v-icon>
                {{
                  props.item.SoldOut ? 'check_box' : 'check_box_outline_blank'
                }}
              </v-icon>
            </td>
          </template>
        </v-data-table>
        <div class="text-xs-center pa-2">
          <v-pagination
            v-model="pagination.page"
            :length="pages"
            :total-visible="7"
            @input="getCommitments"
          />
        </div>
      </v-flex>
    </v-layout>
    <v-divider />
    <v-card-actions v-if="allowLink">
      <v-spacer />
      <v-btn
        flat
        small
        @click="link()"
        color="primary"
        :disabled="selected.length === 0"
        >Rattacher</v-btn
      >
    </v-card-actions>
  </v-card>
</template>

<script>
import * as types from '../store/types'
export default {
  name: 'UnlinkedCommitments',
  data() {
    return {
      search: '',
      year: 0,
      headers: [
        { text: 'Date', value: 'CreationDate' },
        { text: 'Budget', value: 'Budget' },
        { text: 'Code', value: 'FullCode' },
        { text: 'Objet', value: 'Name' },
        { text: 'Bénéficiaire', value: 'Beneficiary' },
        { text: 'Montant', value: 'Value' },
        { text: 'Soldé', value: 'SoldOut' }
      ],
      pagination: {},
      selected: []
    }
  },
  props: {
    destID: { type: Number, required: true },
    allowLink: { type: Boolean, default: false },
    type: {
      validator: function(t) {
        return ['Copro', 'RenewProject', 'Housing'].indexOf(t) !== -1
      }
    }
  },
  methods: {
    newSearch(val) {
      this.search = val
      this.getCommitments()
    },
    newYear(val) {
      if (/^20\d{2}$/.test(val)) {
        this.year = Number(val)
        this.getCommitments()
      }
      if (val === '' || !val) {
        this.year = 0
        this.getCommitments()
      }
    },
    yearRule(val) {
      return /^20\d{2}$/.test(val) || val === '' || 'Année attendue'
    },
    toggleAll() {
      if (this.selected.length) this.selected = []
      else this.selected = this.items.slice()
    },
    async getCommitments() {
      await this.$store.dispatch(types.GET_UNLINKED_COMMITMENTS, {
        Year: this.year,
        Search: this.search,
        Page: this.pagination.page
      })
    },
    link() {
      if (this.destID !== 0) {
        let IDs = this.selected.map(i => i.ID)
        this.$store.dispatch(types.LINK_COMMITMENTS, {
          DestID: this.destID,
          Type: this.type,
          IDs
        })
      }
    }
  },
  computed: {
    items() {
      return this.$store.state.commitments.unlinkedCommitmentsList.map(c => ({
        ID: c.ID,
        Year: c.Year,
        FullCode: c.Code + '/' + c.Number + '/' + c.Line,
        CreationDate: c.CreationDate,
        Name: c.Name,
        Beneficiary: c.BeneficiaryName,
        Value: c.Value,
        Budget: '[' + c.Sector + '] ' + c.ActionName,
        CoproID: c.CoproID,
        RenewProjectID: c.RenewProjectID,
        HousingID: c.HousingID,
        Linked: c.CoproID || c.RenewProjectID || c.HousingID,
        SoldOut: c.SoldOut
      }))
    },
    loading() {
      return this.$store.state.loading.loading !== 0
    },
    itemsCount() {
      return this.$store.state.commitments.unlinkedCommitmentsItemsCount
    },
    pages() {
      if (this.pagination.rowsPerPage == null || this.itemsCount == null)
        return 0

      return Math.ceil(this.itemsCount / this.pagination.rowsPerPage)
    }
  },
  mounted() {
    this.getCommitments()
  }
}
</script>
