<template>
  <v-card flat>
    <v-layout row wrap>
      <v-flex xs12 md6 offset-md3>
        <v-text-field label="Rechercher" v-model="search" />
      </v-flex>
      <v-flex xs12>
        <v-data-table
          :headers="headers"
          :items="items"
          item-key="ID"
          :loading="loading"
          :search="search"
          class="elevation-1"
        >
          <template slot="items" slot-scope="props">
            <td class="text-xs-left">{{ props.item.Name }}</td>
            <td class="text-xs-left">{{ props.item.FullName }}</td>
            <td class="pr-0 pl-0">
              <v-tooltip left>
                <template v-slot:activator="{ on }">
                  <v-btn
                    color="primary"
                    small
                    flat
                    icon
                    class="pa-0"
                    @click="modify(props.item)"
                    v-on="on"
                  >
                    <v-icon>create</v-icon>
                  </v-btn>
                </template>
                Modifier
              </v-tooltip>
            </td>
            <td class="pr-0 pl-0">
              <v-tooltip left>
                <template v-slot:activator="{ on }">
                  <v-btn
                    color="primary"
                    small
                    icon
                    flat
                    class="pa-0"
                    v-on="on"
                    @click="remove(props.item)"
                  >
                    <v-icon>delete</v-icon>
                  </v-btn>
                </template>
                Supprimer
              </v-tooltip>
            </td>
          </template>
        </v-data-table>
      </v-flex>
    </v-layout>
    <v-divider />
    <v-card-actions>
      <v-spacer />
      <v-btn color="primary" small flat @click="add">Créer</v-btn>
    </v-card-actions>

    <v-dialog v-model="dialog" persistent :overlay="false" max-width="500px">
      <v-card>
        <v-card-title class="primary white--text headline">
          {{ dlgTitle }}
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout row wrap="">
              <v-flex xs12>
                <v-text-field
                  label="Code secteur"
                  v-model="item.Name"
                  @keyup.esc="dialog = false"
                  @keyup.enter="save"
                />
              </v-flex>
              <v-flex xs12>
                <v-text-field
                  label="Nom complet"
                  v-model="item.FullName"
                  @keyup.esc="dialog = false"
                  @keyup.enter="save"
                />
              </v-flex>
            </v-layout>
          </v-container>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" small flat @click="dialog = false">
            Annuler
          </v-btn>
          <v-btn color="primary" small flat @click="save" :disabled="disabled">
            {{ dlgBtn }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <delete-dialog
      v-model="delDlg"
      sentence="Supprimer le secteur budgétaire définitivement ?"
      @confirm="delConfirm"
    />
  </v-card>
</template>

<script>
import * as types from '../store/types.js'
import DeleteDialog from './DeleteDialog'
export default {
  name: 'BudgetSector',
  components: { DeleteDialog },
  data() {
    return {
      search: '',
      headers: [
        { text: 'Code', value: 'Name' },
        { text: 'Nom complet', value: 'FullName' },
        { text: '', value: '', sortable: false, width: '1%' },
        { text: '', value: '', sortable: false, width: '1%' }
      ],
      dialog: false,
      delDlg: false,
      item: { Name: '', FullName: null },
      dlgTitle: 'Nouveau secteur budgétaire',
      dlgBtn: 'Créer'
    }
  },
  methods: {
    remove(item) {
      this.item.ID = item.ID
    },
    modify(item) {
      this.item.ID = item.ID
      this.item.Name = item.Name
      this.item.FullName = item.FullName
      this.dlgTitle = 'Modifier le secteur budgétaire'
      this.dlgBtn = 'Modifier'
      this.dialog = true
    },
    add(item) {
      this.item.ID = null
      this.item.Name = ''
      this.item.FullName = null
      this.dlgTitle = 'Créer un secteur budgétaire'
      this.dlgBtn = 'Créer'
      this.dialog = true
    },
    async save() {
      if (!this.disabled) {
        let dispatch =
          this.dlgBtn === 'Créer' ? types.CREATE_SECTOR : types.UPDATE_SECTOR
        await this.$store.dispatch(dispatch, this.item)
        this.dialog = false
      }
    },
    async delConfirm() {
      await this.$store.dispatch(types.DEL_SECTOR, this.item.ID)
      this.delDlg = false
    }
  },
  computed: {
    items() {
      return this.$store.state.settings.budgetSectorsList
    },
    loading() {
      return this.$store.state.loading.loading !== 0
    },
    disabled() {
      return this.item.Name === ''
    }
  }
}
</script>
