<template>
  <v-card flat>
    <v-layout row wrap>
      <v-flex xs12 sd6 offset-sd3 pa-2>
        <v-text-field
          label="Rechercher (code, nom)"
          v-debounce:500ms="newSearch"
        />
      </v-flex>
      <v-flex xs12>
        <v-data-table
          :headers="headers"
          :items="items"
          item-key="InseeCode"
          :loading="loading"
          :rows-per-page-items="[10]"
          :total-items="itemsCount"
          :pagination.sync="pagination"
          hide-actions
          class="elevation-1"
        >
          <template slot="items" slot-scope="props">
            <td class="text-xs-left">{{ props.item.InseeCode }}</td>
            <td class="text-xs-left">{{ props.item.Name }}</td>
            <td class="text-xs-left">
              {{ props.item.CommunityName | nullable }}
            </td>
            <td>
              <v-icon>
                {{ props.item.QPV ? 'check_box' : 'check_box_outline_blank' }}
              </v-icon>
            </td>
            <td class="pr-0 pl-0">
              <v-tooltip left>
                <template v-slot:activator="{ on }">
                  <v-btn
                    color="primary"
                    small
                    flat
                    icon
                    class="pa-0"
                    @click="modify(props.item)"
                    v-on="on"
                  >
                    <v-icon>create</v-icon>
                  </v-btn>
                </template>
                Modifier
              </v-tooltip>
            </td>
            <td class="pr-0 pl-0">
              <v-tooltip left>
                <template v-slot:activator="{ on }">
                  <v-btn
                    color="primary"
                    small
                    icon
                    flat
                    class="pa-0"
                    v-on="on"
                    @click="remove(props.item)"
                  >
                    <v-icon>delete</v-icon>
                  </v-btn>
                </template>
                Supprimer
              </v-tooltip>
            </td>
          </template>
        </v-data-table>
        <div class="text-xs-center pa-2">
          <v-pagination
            v-model="pagination.page"
            :length="pages"
            :total-visible="7"
            @input="getCities"
          />
        </div>
      </v-flex>
    </v-layout>
    <v-divider />
    <v-card-actions>
      <v-spacer />
      <upload-button @fileChosen="upload($event)" />
      <v-btn color="primary" small flat @click="add">Créer</v-btn>
    </v-card-actions>

    <v-dialog v-model="dialog" persistent :overlay="false" max-width="500px">
      <v-card>
        <v-card-title class="primary white--text headline">{{
          dlgTitle
        }}</v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout row wrap>
              <v-flex xs12>
                <v-text-field
                  label="Code INSEE"
                  v-model="item.InseeCode"
                  @keyup.esc="dialog = false"
                  @keyup.enter="save"
                />
              </v-flex>
              <v-flex xs12>
                <v-text-field
                  label="Nom"
                  v-model="item.Name"
                  @keyup.esc="dialog = false"
                  @keyup.enter="save"
                />
              </v-flex>
              <v-flex xs12>
                <v-autocomplete
                  :items="communities"
                  item-text="Name"
                  item-value="ID"
                  v-model="item.CommunityID"
                  label="Intercommunalité"
                />
              </v-flex>
              <v-flex xs12>
                <v-checkbox label="QPV" v-model="item.QPV" />
              </v-flex>
            </v-layout>
          </v-container>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" small flat @click="dialog = false"
            >Annuler</v-btn
          >
          <v-btn
            color="primary"
            small
            flat
            @click="save"
            :disabled="disabled"
            >{{ dlgBtn }}</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>

    <delete-dialog
      v-model="delDlg"
      sentence="Supprimer la commune définitivement ?"
      @confirm="delConfirm"
    />
  </v-card>
</template>

<script>
import * as types from '../store/types.js'
import UploadButton from './UploadButton.vue'
import DeleteDialog from './DeleteDialog'
export default {
  name: 'Cities',
  components: { UploadButton, DeleteDialog },
  data() {
    return {
      search: '',
      headers: [
        { text: 'Code', value: 'InseeCode' },
        { text: 'Nom', value: 'Name' },
        { text: 'Interco', value: 'Community' },
        { text: 'QPV', value: 'QPV' },
        { text: '', value: '', sortable: false, width: '1%' },
        { text: '', value: '', sortable: false, width: '1%' }
      ],
      dialog: false,
      delDlg: false,
      item: { InseeCode: null, Name: null, CommunityID: null, QPV: false },
      dlgTitle: 'Nouvelle commune',
      dlgBtn: 'Créer',
      menu: null,
      pagination: {}
    }
  },
  methods: {
    newSearch(val) {
      this.search = val
      this.getCities()
    },
    remove(item) {
      this.item.InseeCode = item.InseeCode
      this.delDlg = true
    },
    modify(item) {
      this.item = { ...item }
      this.dlgTitle = `Modifier la commune`
      this.dlgBtn = 'Modifier'
      this.dialog = true
    },
    add(item) {
      this.item.Name = null
      this.item.InseeCode = null
      this.item.CommunityID = null
      this.item.QPV = false
      this.dlgTitle = 'Nouvelle commune'
      this.dlgBtn = 'Créer'
      this.dialog = true
    },
    async save() {
      if (!this.disabled) {
        let dispatch =
          this.dlgBtn === 'Créer' ? types.CREATE_CITY : types.UPDATE_CITY
        await this.$store.dispatch(dispatch, {
          Name: this.item.Name,
          InseeCode: parseInt(this.item.InseeCode),
          CommunityID: this.item.CommunityID,
          QPV: this.item.QPV
        })
        this.dialog = false
      }
    },
    async delConfirm() {
      await this.$store.dispatch(types.DEL_CITY, this.item.InseeCode)
      this.delDlg = false
    },
    upload(file) {
      this.$store.dispatch(types.UPLOAD_CITIES, file)
    },
    async getCities() {
      await this.$store.dispatch(types.GET_PAGINATED_CITIES, {
        Search: this.search,
        Page: this.pagination.page || 0
      })
    }
  },
  computed: {
    communities() {
      return this.$store.state.communities.communitiesList
    },
    items() {
      return this.$store.state.cities.citiesList
    },
    itemsCount() {
      return this.$store.state.cities.citiesItemsCount
    },
    pages() {
      if (this.pagination.rowsPerPage == null || this.itemsCount == null)
        return 0

      return Math.ceil(this.itemsCount / this.pagination.rowsPerPage)
    },
    loading() {
      return this.$store.state.loading.loading !== 0
    },
    disabled() {
      return (
        this.item.Name === null ||
        this.item.Name === '' ||
        !/^\d{5}$/.test(this.item.InseeCode)
      )
    }
  }
}
</script>
