<template>
  <v-card>
    <v-card-title class="white--text primary headline">
      [{{ project ? project.Reference : '' }}]
      {{ project ? project.Name : '' }}
    </v-card-title>
    <v-card-text>
      <v-layout row wrap>
        <table>
          <tr>
            <td class="body-2">Type :</td>
            <td>{{ project ? (project.PRIN ? 'PRIN' : 'PRIR') : '' }}</td>
          </tr>
          <tr>
            <td class="body-2">Commune(s) :</td>
            <td>{{ cities }}</td>
          </tr>
          <tr>
            <td class="body-2">Enveloppe régionale :</td>
            <td>{{ project ? project.Budget : null | currency }}</td>
          </tr>
          <tr>
            <td class="body-2">Catégorie région :</td>
            <td>{{ project ? project.CompositeIndex : null | number }}</td>
          </tr>
        </table>
      </v-layout>
    </v-card-text>
    <v-tabs color="secondary" v-model="active" slider-color="primary">
      <v-tab ripple>Engagements</v-tab>
      <v-tab-item>
        <v-data-table
          :headers="commitmentHeaders"
          :items="commitments"
          class="elevation-1"
          item-key="ID"
          :loading="loading"
          :search="commitmentSearch"
        >
          <template slot="items" slot-scope="props">
            <td class="text-xs-left">{{ props.item.CreationDate | date }}</td>
            <td class="text-xs-left">
              {{ props.item.ModificationDate | date }}
            </td>
            <td class="text-xs-left">{{ props.item.BeneficiaryName }}</td>
            <td class="text-xs-left">{{ props.item.Name }}</td>
            <td class="text-xs-left">{{ props.item.IrisCode }}</td>
            <td class="text-xs-right">{{ props.item.Value | currency }}</td>
            <td class="text-xs-center">
              <v-icon>
                {{
                  props.item.SoldOut ? 'check_box' : 'check_box_outline_blank'
                }}
              </v-icon>
            </td>
          </template>
          <template slot="footer">
            <td class="body-2 text-xs-left">Total</td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td class="text-xs-right body-2">
              {{ totalCommitments | currency }}
            </td>
            <td></td>
          </template>
        </v-data-table>
        <v-btn color="primary" flat @click="commitmentsExport"
          >Export Excel</v-btn
        >
      </v-tab-item>
      <v-tab ripple>Paiements</v-tab>
      <v-tab-item>
        <v-data-table
          :headers="paymentHeaders"
          :items="payments"
          class="elevation-1"
          item-key="ID"
          :loading="loading"
          search="paymentSearch"
        >
          <template slot="items" slot-scope="props">
            <td class="text-xs-left">{{ props.item.CreationDate | date }}</td>
            <td class="text-xs-left">
              {{ props.item.ModificationDate | date }}
            </td>
            <td class="text-xs-left">{{ props.item.Value | currency }}</td>
          </template>
          <template slot="footer">
            <td class="body-2 text-xs-left">Total</td>
            <td></td>
            <td class="text-xs-right body-2">{{ totalPayments }}</td>
          </template>
        </v-data-table>
        <v-btn color="primary" flat @click="paymentsExport">Export Excel</v-btn>
      </v-tab-item>
      <v-tab ripple>Prévisions</v-tab>
      <v-tab-item>
        <v-layout row wrap>
          <v-flex xs12 sm6 offset-sm3>
            <v-text-field label="Rechercher" v-model="forecastSearch" />
          </v-flex>
          <v-flex xs12>
            <v-data-table
              :headers="forecastHeaders"
              :items="forecasts"
              class="elevation-1"
              item-key="ID"
              :loading="loading"
              :search="forecastSearch"
            >
              <template slot="items" slot-scope="props">
                <td class="text-xs-left">
                  {{ props.item.CommissionDate | date }}
                </td>
                <td class="text-xs-left">{{ props.item.CommissionName }}</td>
                <td class="text-xs-right">{{ props.item.Value | currency }}</td>
                <td class="text-xs-right">{{ props.item.Comment }}</td>
                <td class="text-xs-center">
                  <v-btn
                    flat
                    icon
                    color="primary"
                    @click="edit(props.item)"
                    v-if="hasRenewProjectRight"
                  >
                    <v-icon>create</v-icon>
                  </v-btn>
                  <v-btn
                    flat
                    icon
                    color="primary"
                    @click="remove(props.item)"
                    v-if="hasRenewProjectRight"
                  >
                    <v-icon>delete</v-icon>
                  </v-btn>
                </td>
              </template>
            </v-data-table>
            <v-btn
              color="primary"
              flat
              @click="addForecast"
              v-if="hasRenewProjectRight"
              >Ajouter</v-btn
            >
          </v-flex>
        </v-layout>
      </v-tab-item>
      <v-tab ripple>Rattachements</v-tab>
      <v-tab-item>
        <unlinked-commitment
          :destID="project ? project.ID : 0"
          type="RenewProject"
          :allowLink="hasRenewProjectRight"
        />
      </v-tab-item>
    </v-tabs>
    <v-dialog :overlay="false" v-model="dialog" persistent max-width="500px">
      <v-card>
        <v-card-title class="primary white--text headline">{{
          dlgTitle
        }}</v-card-title>
        <v-card-text>
          <v-layout row wrap>
            <v-flex xs12>
              <v-autocomplete
                v-model="forecast.CommissionID"
                :items="commissions"
                :loading="loading"
                hide-no-data
                hide-selected
                item-text="Name"
                item-value="ID"
                label="Commission"
                required
                :rules="[notNull]"
              />
            </v-flex>
            <v-flex xs12>
              <v-text-field
                label="Montant"
                v-model="forecast.Value"
                :rules="[valueRequired]"
              />
            </v-flex>
            <v-flex xs12>
              <v-textarea label="Commentaire" v-model="forecast.Comment" />
            </v-flex>
          </v-layout>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" small flat @click="dialog = false"
            >Annuler</v-btn
          >
          <v-btn
            color="primary"
            small
            flat
            @click="save"
            :disabled="disabled"
            >{{ dlgBtn }}</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>

    <delete-dialog
      v-model="delDlg"
      sentence="Supprimer la prévision définitivement ?"
      @confirm="delConfirm"
    />
  </v-card>
</template>
<script>
import * as types from '../store/types'
import UnlinkedCommitment from './UnlinkedCommitments.vue'
import { excelExport } from '../excel'
import DeleteDialog from './DeleteDialog'
const checkValue = v => /^\d+([,.]\d{1,2})?$/.test(v)
const input2Value = v => {
  if (typeof v === 'number') return 100 * v
  if (typeof v === 'string') return parseInt(100 * v.replace(',', '.'))
  return v
}
export default {
  name: 'RenewProject',
  components: { UnlinkedCommitment, DeleteDialog },
  data() {
    return {
      commitmentHeaders: [
        { text: 'Date création', value: 'CreationDate', align: 'center' },
        { text: 'Date modif.', value: 'ModificationDate', align: 'center' },
        { text: 'Bénéficiaire', value: 'BeneficiaryName', align: 'center' },
        { text: 'Objet', value: 'Name', align: 'center' },
        { text: 'IRIS', value: 'IrisCode', align: 'center' },
        { text: 'Montant', value: 'Value', align: 'center' },
        { text: 'Soldé', value: 'SoldOut', align: 'center' }
      ],
      commitmentSearch: null,
      paymentHeaders: [
        { text: 'Date création', value: 'CreationDate', align: 'center' },
        { text: 'Date modif.', value: 'ModificationDate', align: 'center' },
        { text: 'Montant', value: 'Value', align: 'center' }
      ],
      forecastHeaders: [
        { text: 'Date', value: 'CommissionDate', align: 'center' },
        { text: 'Nom commission', value: 'CommissionName', align: 'center' },
        { text: 'Montant', value: 'Value', align: 'center' },
        { text: 'Commentaire', value: 'Comment', align: 'center' },
        { text: 'Actions', value: '', align: 'center' }
      ],
      paymentSearch: null,
      forecastSearch: null,
      active: null,
      forecast: {
        ID: 0,
        CommissionID: null,
        Value: null,
        Comment: null,
        RenewProjectID: this.project ? this.project.ID : 0
      },
      dialog: false,
      dlgTitle: 'Créer une nouvelle prévision',
      dlgBtn: 'Créer',
      delDlg: false,
      delID: null
    }
  },
  methods: {
    commitmentsExport() {
      const columns = [
        { header: 'Année', key: 'Year', width: 6 },
        {
          header: 'Création',
          key: 'CreationDate',
          width: 10,
          style: { numberFormat: 'dd/mm/yy' }
        },
        {
          header: 'Modification',
          key: 'ModificationDate',
          width: 10,
          style: { numberFormat: 'dd/mm/yy' }
        },
        { header: 'Code', key: 'Code', width: 10 },
        { header: 'Numéro', key: 'Number', width: 10 },
        { header: 'Ligne', key: 'Line', width: 6 },
        { header: 'Libellé', key: 'Name', width: 20 },
        {
          header: 'Montant',
          key: 'Value',
          width: 14,
          style: { numberFormat: '#,##0.00' },
          addTotal: true
        },
        { header: 'Bénéficiaire', key: 'BeneficiaryName', width: 20 },
        { header: 'Code IRIS', key: 'IrisCode', width: 10 },
        { header: 'Soldé', key: 'SoldOut', width: 6 }
      ]
      const formattedCommitments = this.commitments.map(
        ({ Value, SoldOut, ...others }) => ({
          Value: 0.01 * Value,
          SoldOut: SoldOut ? 'Oui' : 'Non',
          ...others
        })
      )
      excelExport(
        formattedCommitments,
        columns,
        'Engagements' + this.project.Reference
      )
    },
    edit(item) {
      this.forecast = { ...item }
      this.forecast.Value *= 0.01
      this.dlgTitle = 'Modifier la prévision'
      this.dlgBtn = 'Modifier'
      this.dialog = true
    },
    remove(item) {
      this.delID = item.ID
      this.delDlg = true
    },
    delConfirm() {
      this.$store.dispatch(types.DELETE_RENEW_PROJECT_FORECAST, this.delID)
      this.delDlg = false
    },
    save() {
      const action =
        this.dlgBtn === 'Créer'
          ? types.CREATE_RENEW_PROJECT_FORECAST
          : types.UPDATE_RENEW_PROJECT_FORECAST
      const item = {
        RenewProjectForecast: {
          ID: this.forecast.ID,
          RenewProjectID: this.project.ID,
          CommissionID: this.forecast.CommissionID,
          Value: input2Value(this.forecast.Value),
          Comment: this.forecast.Comment
        }
      }
      this.$store.dispatch(action, item)
      this.dialog = false
    },
    addForecast() {
      this.forecast = {
        ID: 0,
        CommissionID: null,
        Value: null,
        Comment: null,
        RenewProjectID: this.project.ID
      }
      this.dlgTitle = 'Créer une nouvelle prévision'
      this.dlgBtn = 'Créer'
      this.dialog = true
    },
    paymentsExport() {},
    notNull(x) {
      return x !== null || 'Une commission doit être choisie'
    },
    valueRequired(x) {
      return checkValue(x) || 'Montant de la prévision nécessaire'
    }
  },
  computed: {
    project() {
      return this.$store.state.renewProject.renewProject
    },
    loading() {
      return this.$store.state.loading.loading !== 0
    },
    cities() {
      const p = this.project
      return p
        ? p.CityName1 +
            (p.CityName2 ? ',' + p.CityName2 : '') +
            (p.CityName3 ? ',' + p.CityName3 : '')
        : ''
    },
    commissions() {
      return this.$store.state.settings.commissionsList
    },
    commitments() {
      return this.$store.state.renewProject.rpCommitments
    },
    totalCommitments() {
      return this.commitments.reduce((a, c) => a + c.Value, 0)
    },
    totalPayments() {
      return this.payments.reduce((a, c) => a + c.Value, 0)
    },
    payments() {
      return this.$store.state.renewProject.rpPayments
    },
    forecasts() {
      return this.$store.state.renewProject.rpForecasts
    },
    disabled() {
      return (
        this.forecast.CommissionID === null || !checkValue(this.forecast.Value)
      )
    },
    hasRenewProjectRight() {
      return this.$store.getters.hasRenewProjectRight
    }
  },
  created() {
    this.$store.dispatch(types.GET_RENEW_PROJECT_DATAS, this.$route.params.ID)
  }
}
</script>
