<template>
  <v-card>
    <v-card-title class="white--text primary headline"
      >Copropriétés</v-card-title
    >
    <v-card-text>
      <v-layout row wrap>
        <v-flex xs12 md6 offset-md3>
          <v-text-field label="Rechercher" v-model="search" />
        </v-flex>
        <v-flex xs12>
          <v-data-table
            :headers="headers"
            :items="copros"
            item-key="ID"
            :loading="loading"
            :search="search"
            class="elevation-1"
          >
            <template slot="items" slot-scope="props">
              <td class="text-xs-left table-link" @click="goTo(props.item.ID)">
                {{ props.item.Reference }}
              </td>
              <td class="text-xs-left table-link" @click="goTo(props.item.ID)">
                {{ props.item.Name }}
              </td>
              <td>{{ props.item.Address }}</td>
              <td class="text-xs-right">{{ props.item.LabelDate | date }}</td>
              <td class="text-xs-right">{{ props.item.Budget | currency }}</td>
              <td class="pr-0 pl-0">
                <v-tooltip left v-if="hasCoproRight">
                  <template v-slot:activator="{ on }">
                    <v-btn
                      color="primary"
                      icon
                      small
                      flat
                      class="pa-0"
                      @click="modify(props.item)"
                    >
                      <v-icon class="pa-0" v-on="on">create</v-icon>
                    </v-btn>
                  </template>
                  <span>Modifier</span>
                </v-tooltip>
              </td>
              <td class="pr-0 pl-0">
                <v-tooltip left v-if="hasCoproRight">
                  <template v-slot:activator="{ on }">
                    <v-btn
                      color="primary"
                      icon
                      small
                      flat
                      class="pa-0"
                      @click="remove(props.item)"
                      v-on="on"
                    >
                      <v-icon class="pa-0" v-on="on">delete</v-icon>
                    </v-btn>
                  </template>
                  <span>Supprimer</span>
                </v-tooltip>
              </td>
            </template>
          </v-data-table>
        </v-flex>
      </v-layout>
    </v-card-text>
    <v-divider />
    <v-card-actions v-if="hasCoproRight">
      <v-spacer />
      <upload-button
        name="Import lien engagements"
        @fileChosen="commitentLinkUpload($event)"
      />
      <upload-button
        name="Import opérations"
        @fileChosen="operationUpload($event)"
      />
      <v-btn color="primary" small flat @click="add">Créer</v-btn>
    </v-card-actions>

    <v-dialog v-model="dialog" persistent :overlay="false" max-width="500px">
      <v-card>
        <v-card-title class="primary white--text headline">
          {{ dlgTitle }}
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout row wrap>
              <v-flex xs6>
                <v-text-field label="Référence" v-model="item.Reference" />
              </v-flex>
              <v-flex xs6>
                <v-text-field label="Nom" v-model="item.Name" />
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Adresse" v-model="item.Address" />
              </v-flex>
              <v-flex xs6>
                <v-text-field label="Code INSEE" v-model="item.ZipCode" />
              </v-flex>
              <v-flex xs6>
                <v-menu
                  ref="menu"
                  v-model="menu"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  :return-value.sync="item.LabelDate"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
                  min-width="290px"
                >
                  <template v-slot:activator="{ on }">
                    <v-text-field
                      v-model="formattedDate"
                      label="Date du label"
                      prepend-icon="event"
                      readonly
                      v-on="on"
                    ></v-text-field>
                  </template>
                  <v-date-picker v-model="item.LabelDate" no-title scrollable>
                    <v-spacer></v-spacer>
                    <v-btn flat color="primary" @click="menu = false"
                      >Annuler</v-btn
                    >
                    <v-btn
                      flat
                      color="primary"
                      @click="$refs.menu.save(item.LabelDate)"
                      >OK</v-btn
                    >
                  </v-date-picker>
                </v-menu>
              </v-flex>
              <v-flex xs6>
                <v-text-field label="Budget" v-model="item.Budget" />
              </v-flex>
            </v-layout>
          </v-container>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" small flat @click="dialog = false"
            >Annuler</v-btn
          >
          <v-btn
            color="primary"
            small
            flat
            @click="save"
            :disabled="disabled"
            >{{ dlgBtn }}</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>

    <delete-dialog
      v-model="delDlg"
      sentence="Supprimer la copropriété définitivement ?"
      @confirm="delConfirm"
    />
  </v-card>
</template>

<script>
import * as types from '../store/types.js'
import { formatNullDate } from '../date.js'
import UploadButton from './UploadButton.vue'
import DeleteDialog from './DeleteDialog'
export default {
  name: 'Copros',
  components: { UploadButton, DeleteDialog },
  data() {
    return {
      search: '',
      headers: [
        { text: 'Référence', align: 'center', value: 'Reference' },
        { text: 'Nom', align: 'center', value: 'Name' },
        { text: 'Adresse', align: 'center', value: 'Address' },
        { text: 'Date label', align: 'center', value: 'LabelDate' },
        { text: 'Budget', align: 'center', value: 'Budget' },
        { text: '', value: '', sortable: false, width: '1%' },
        { text: '', value: '', sortable: false, width: '1%' }
      ],
      dialog: false,
      item: {
        ID: null,
        Reference: '',
        Name: '',
        Address: '',
        ZipCode: 0,
        LabelDate: null,
        Budget: null
      },
      dlgTitle: 'Créer une copropriété',
      dlgBtn: 'Créer',
      delDlg: false,
      menu: false
    }
  },
  computed: {
    copros() {
      return this.$store.state.copros.coprosList.map(c => ({
        ID: c.ID,
        Address: '[' + c.CityName + '] ' + c.Address,
        Budget: c.Budget,
        LabelDate: c.LabelDate,
        Name: c.Name,
        Reference: c.Reference,
        ZipCode: c.ZipCode
      }))
    },
    loading() {
      return this.$store.state.loading.loading !== 0
    },
    disabled() {
      return (
        this.item.Reference === '' ||
        this.item.Name === '' ||
        this.item.Address === '' ||
        this.item.ZipCode === 0 ||
        (this.item.Budget && !/^\d+[,.]?\d{0,2}$/.test(this.item.Budget))
      )
    },
    formattedDate() {
      return formatNullDate(this.item.LabelDate)
    },
    hasCoproRight() {
      return this.$store.getters.hasCoproRight
    }
  },
  methods: {
    add() {
      this.item = {
        ID: null,
        Reference: '',
        Name: '',
        Address: '',
        ZipCode: 0,
        LabelDate: null,
        Budget: null
      }
      this.dlgTitle = 'Créer une copropriété'
      this.dlgBtn = 'Créer'
      this.dialog = true
    },
    modify(item) {
      let i = this.copros.find(i => i.ID === item.ID)
      this.item = {
        ID: i.ID,
        Reference: i.Reference,
        Name: i.Name,
        Address: i.Address,
        ZipCode: i.ZipCode,
        LabelDate: i.LabelDate,
        Budget: i.Budget ? i.Budget / 100 : null
      }
      this.dlgTitle = 'Modifier une copropriété'
      this.dlgBtn = 'Modifier'
      this.dialog = true
    },
    remove(item) {
      this.item.ID = item.ID
      this.delDlg = true
    },
    async delConfirm() {
      await this.$store.dispatch(types.DEL_COPRO, this.item.ID)
      this.delDlg = false
    },
    async save() {
      if (!this.disabled) {
        let dispatch =
          this.dlgBtn === 'Créer' ? types.CREATE_COPRO : types.UPDATE_COPRO
        let labelDate = null
        if (this.item.LabelDate) {
          let date = new Date(this.item.LabelDate)
          labelDate = date.toISOString()
        }
        this.$store.dispatch(dispatch, {
          ID: this.item.ID,
          Reference: this.item.Reference,
          Name: this.item.Name,
          ZipCode: parseInt(this.item.ZipCode),
          Address: this.item.Address,
          LabelDate: labelDate,
          Budget: this.item.Budget
            ? 100 * parseInt(String(this.item.Budget).replace(',', '.'))
            : null
        })
        this.dialog = false
      }
    },
    operationUpload(file) {
      this.$store.dispatch(types.UPLOAD_COPROS, file)
    },
    commitentLinkUpload(file) {
      this.$store.dispatch(types.UPLOAD_COPRO_COMMITMENT_LINK, file)
    },
    goTo(ID) {
      this.$router.push({ name: 'Copro', params: { ID } })
    }
  },
  created() {
    this.$store.dispatch(types.GET_COPROS)
  }
}
</script>
