<template>
  <v-card flat>
    <v-layout row wrap>
      <v-flex xs12 sm6 offset-sm3 pa-2>
        <v-text-field
          label="Rechercher (code, nom)"
          v-debounce:500ms="newSearch"
        />
      </v-flex>
      <v-flex xs12>
        <v-data-table
          :headers="headers"
          :items="items"
          item-key="ID"
          :loading="loading"
          :rows-per-page-items="[10]"
          :total-items="itemsCount"
          :pagination.sync="pagination"
          hide-actions
          class="elevation-1"
        >
          <template slot="items" slot-scope="props">
            <td class="text-xs-left">{{ props.item.Code }}</td>
            <td class="text-xs-left">{{ props.item.Name }}</td>
            <td>
              <v-btn small flat color="primary" @click="details(props.item)"
                >Détails</v-btn
              >
            </td>
          </template>
        </v-data-table>
        <div class="text-xs-center pa-2">
          <v-pagination
            v-model="pagination.page"
            :length="pages"
            :total-visible="7"
          />
        </div>
      </v-flex>
    </v-layout>
  </v-card>
</template>

<script>
import * as types from '../store/types.js'
export default {
  name: 'Beneficiaries',
  data() {
    return {
      search: '',
      year: 0,
      headers: [
        { text: 'Code', value: 'Code', sortable: false },
        { text: 'Nom', value: 'Name', sortable: false },
        { text: 'Détails', value: '', sortable: false }
      ],
      pagination: {}
    }
  },
  methods: {
    newSearch(val) {
      this.search = val
      this.getBeneficiaries()
    },
    async getBeneficiaries() {
      await this.$store.dispatch(types.GET_BENEFICIARIES, {
        Search: this.search,
        Page: this.pagination.page
      })
    },
    details({ Name, ID }) {
      this.$router.push({ name: 'Beneficiary', params: { ID, Name } })
    }
  },
  mounted() {
    this.$store.dispatch(types.GET_BENEFICIARIES, {
      Page: 1,
      Search: this.search
    })
  },
  computed: {
    items() {
      return this.$store.state.beneficiaries.beneficiariesList
    },
    loading() {
      return this.$store.state.loading.loading !== 0
    },
    itemsCount() {
      return this.$store.state.beneficiaries.beneficiariesItemsCount
    },
    pages() {
      if (this.pagination.rowsPerPage == null || this.itemsCount == null)
        return 0

      return Math.ceil(this.itemsCount / this.pagination.rowsPerPage)
    }
  },
  watch: {
    pagination: {
      handler() {
        this.getBeneficiaries()
      },
      deep: true
    }
  }
}
</script>
