<template>
  <v-card>
    <v-card-title class="white--text primary headline">
      Utilisateurs
    </v-card-title>
    <v-card-text>
      <v-data-table
        :headers="headers"
        :items="items"
        class="elevation-1"
        item-key="ID"
        loading="true"
        :search="search"
      >
        <template slot="items" slot-scope="props">
          <td class="text-xs-left">{{ props.item.Name }}</td>
          <td class="text-xs-center">
            <v-icon>
              {{
                props.item.isActive ? 'check_box' : 'check_box_outline_blank'
              }}
            </v-icon>
          </td>
          <td class="text-xs-center">
            <v-icon>
              {{ props.item.isAdmin ? 'check_box' : 'check_box_outline_blank' }}
            </v-icon>
          </td>
          <td class="text-xs-center">
            <v-icon>
              {{ props.item.isCopro ? 'check_box' : 'check_box_outline_blank' }}
            </v-icon>
          </td>
          <td class="text-xs-center">
            <v-icon>
              {{
                props.item.isRenewProject
                  ? 'check_box'
                  : 'check_box_outline_blank'
              }}
            </v-icon>
          </td>
          <td class="pr-0 pl-0">
            <v-tooltip left>
              <template v-slot:activator="{ on }">
                <v-btn
                  color="primary"
                  small
                  flat
                  icon
                  class="pa-0"
                  @click="showModifyDlg(props.item)"
                  v-on="on"
                >
                  <v-icon>create</v-icon>
                </v-btn>
              </template>
              Modifier
            </v-tooltip>
          </td>
          <td class="pr-0 pl-0">
            <v-tooltip left>
              <template v-slot:activator="{ on }">
                <v-btn
                  color="primary"
                  small
                  icon
                  flat
                  class="pa-0"
                  v-on="on"
                  @click="showDelDlg(props.item)"
                >
                  <v-icon>delete</v-icon>
                </v-btn>
              </template>
              Supprimer
            </v-tooltip>
          </td>
        </template>
      </v-data-table>
    </v-card-text>
    <v-divider />
    <v-card-actions>
      <v-spacer />
      <v-btn color="primary" flat @click="showCreateDlg">
        Nouvel utilisateur
      </v-btn>
    </v-card-actions>
    <v-dialog
      v-model="usrDialog"
      class="title primary white--text"
      max-width="500px"
    >
      <v-card>
        <v-card-title class="title primary white--text">
          {{ dlgTitle }}
        </v-card-title>
        <v-card-text>
          <v-layout row wrap>
            <v-flex xs12>
              <v-text-field
                v-model="modifiedItem.Name"
                label="Nom"
                required
                :rules="[notEmpty]"
              />
            </v-flex>
            <v-flex xs12>
              <v-text-field
                v-model="modifiedItem.Email"
                label="Email"
                required
                :rules="[notEmpty]"
              />
            </v-flex>
            <v-flex xs12>
              <v-text-field v-model="password" label="Mot de passe" />
            </v-flex>
            <v-flex xs6>
              <v-checkbox
                :input-value="modifiedItem.isActive"
                label="Actif"
                @change="change(modifiedItem, 'isActive')"
                primary
                hide-details
              />
            </v-flex>
            <v-flex xs6>
              <v-checkbox
                :input-value="modifiedItem.isAdmin"
                label="Administrateur"
                @change="change(modifiedItem, 'isAdmin')"
                primary
                hide-details
              />
            </v-flex>
            <v-flex xs6>
              <v-checkbox
                :input-value="modifiedItem.isCopro"
                label="Droits copro"
                @change="change(modifiedItem, 'isCopro')"
                primary
                hide-details
              />
            </v-flex>
            <v-flex xs6>
              <v-checkbox
                :input-value="modifiedItem.isRenewProject"
                label="Droits (N)PNRU"
                @change="change(modifiedItem, 'isRenewProject')"
                primary
                hide-details
              />
            </v-flex>
          </v-layout>
        </v-card-text>
        <v-divider />
        <v-card-actions>
          <v-spacer />
          <v-btn color="primary" flat @click="usrDialog = false">Annuler</v-btn>
          <v-btn color="primary" flat @click="saveDlg" :disabled="disabled">
            {{ saveBtnTxt }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog
      v-model="delDialog"
      class="title primary white--text"
      max-width="500px"
    >
      <v-card>
        <v-card-title class="title primary white--text">
          Suppression d'utilisateur
        </v-card-title>
        <v-card-text>
          Supprimer définitivement l'utilisateur {{ this.modifiedItem.Name }} ?
        </v-card-text>
        <v-divider />
        <v-card-actions>
          <v-spacer />
          <v-btn color="primary" flat @click="delDialog = false">Annuler</v-btn>
          <v-btn color="primary" flat @click="delDlg">
            Supprimer
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>
import * as types from '../store/types.js'

export default {
  name: 'Users',
  data() {
    return {
      search: '',
      headers: [
        { text: 'Nom', value: 'Name' },
        { text: 'Actif', value: 'isActive' },
        { text: 'Admin', value: 'isAdmin' },
        { text: 'Copro', value: 'isCopro' },
        { text: 'RU', value: 'isRenewProject' },
        { text: '', value: '', sortable: false, width: '1%' },
        { text: '', value: '', sortable: false, width: '1%' }
      ],
      items: [],
      usrDialog: false,
      delDialog: false,
      modifiedItem: {},
      password: '',
      disabled: false,
      saveBtnTxt: '',
      dlgTitle: ''
    }
  },
  computed: {
    usersList() {
      return this.$store.state.users.usersList
    }
  },
  methods: {
    change(item, field) {
      item.Updated = true
      item[field] = !item[field]
      this.modified = true
    },
    showModifyDlg(item) {
      this.modifiedItem = { ...item }
      this.usrDialog = true
      this.dlgTitle = `Modification d'utilisateur`
      this.saveBtnTxt = 'Modifier'
    },
    showCreateDlg(item) {
      this.modifiedItem = {
        Name: '',
        Email: '',
        isActive: false,
        isAdmin: false,
        isCopro: false,
        isRenewProject: false
      }
      this.usrDialog = true
      this.dlgTitle = `Création d'utilisateur`
      this.saveBtnTxt = 'Créer'
    },
    showDelDlg(item) {
      this.modifiedItem = { ...item }
      this.delDialog = true
    },
    notEmpty(text) {
      return text ? text.length > 0 || 'À remplir' : `À remplir`
    },
    async saveDlg() {
      if (!this.disabled) {
        this.modifiedItem.Rights =
          (this.modifiedItem.isActive ? 1 : 0) +
          (this.modifiedItem.isAdmin ? 4 : 0) +
          (this.modifiedItem.isCopro ? 8 : 0) +
          (this.modifiedItem.isRenewProject ? 16 : 0)
        this.modifiedItem.Password = this.password
        let action =
          this.saveBtnTxt === 'Modifier' ? types.UPDATE_USER : types.CREATE_USER
        await this.$store.dispatch(action, this.modifiedItem)
        this.usrDialog = false
        this.password = ''
      }
    },
    async delDlg() {
      await this.$store.dispatch(types.DEL_USER, this.modifiedItem.ID)
      this.delDialog = false
    }
  },
  watch: {
    usersList(newList) {
      this.items = newList.map((u, i) => ({
        ID: u.ID,
        Name: u.Name,
        Email: u.Email,
        Updated: false,
        isActive: u.Rights & 1,
        isAdmin: u.Rights & 4,
        isCopro: u.Rights & 8,
        isRenewProject: u.Rights & 16
      }))
      this.modified = false
    }
  },
  mounted() {
    this.$store.dispatch(types.GET_USERS)
  }
}
</script>
