import Vue from 'vue'
import Router from 'vue-router'
import Users from './components/Users.vue'
import Housings from './components/Housings.vue'
import Copros from './components/Copros.vue'
import RenewProjects from './components/RenewProjects'
import Settings from './components/Settings'
import Home from './components/Home'
import Beneficiaries from './components/Beneficiaries'
import Beneficiary from './components/Beneficiary'
import ForeCasts from './components/Forecasts'
import RenewProject from './components/RenewProject'
import Copro from './components/Copro'

Vue.use(Router)

export default new Router({
  routes: [
    { path: '/', component: Home, name: 'Home' },
    { path: '/users', component: Users, name: 'Users' },
    { path: '/housings', component: Housings, name: 'Housings' },
    { path: '/copros', component: Copros, name: 'Copros' },
    {
      path: '/renew_projects',
      component: RenewProjects,
      name: 'RenewProjects'
    },
    { path: '/settings', component: Settings, name: 'Settings' },
    { path: '/beneficiaries', component: Beneficiaries, name: 'Beneficiaries' },
    { path: '/beneficiary', component: Beneficiary, name: 'Beneficiary' },
    { path: '/forecasts', component: ForeCasts, name: 'Forecasts' },
    { path: '/renew_project', component: RenewProject, name: 'RenewProject' },
    { path: '/copro', component: Copro, name: 'Copro' }
  ]
})
