<template>
  <v-card>
    <v-card-title class="white--text primary headline"
      >Situation de {{ beneficiaryName }}</v-card-title
    >
    <v-card-text>
      <v-layout row wrap>
        <v-flex xs12 sm6 pr-2>
          <v-text-field
            label="Rechercher (libellé)"
            v-model="search"
            v-debounce:500ms="newSearch"
          />
        </v-flex>
        <v-flex xs12 sm6>
          <v-text-field
            label="À partir de l'année"
            v-model="year"
            v-debounce:500ms="yearChanged"
          />
        </v-flex>
        <v-flex xs12>
          <v-data-table
            :headers="headers"
            :items="items"
            item-key="ID"
            :loading="loading"
            :rows-per-page-items="[10]"
            :total-items="itemsCount"
            :pagination.sync="pagination"
            hide-actions
            class="elevation-1"
          >
            <template slot="items" slot-scope="props">
              <td class="text-xs-right">{{ props.item.Date | date }}</td>
              <td>{{ props.item.Name }}</td>
              <td class="text-xs-right">{{ props.item.Value | currency }}</td>
              <td class="text-xs-right">
                {{ props.item.Available | currency }}
              </td>
            </template>
          </v-data-table>
          <div class="text-xs-center pa-2">
            <v-pagination
              v-model="pagination.page"
              :length="pages"
              :total-visible="7"
              @input="getDatas"
            />
          </div>
        </v-flex>
      </v-layout>
    </v-card-text>
  </v-card>
</template>

<script>
import * as types from '../store/types.js'
export default {
  name: 'Beneficiary',
  data() {
    return {
      search: '',
      pagination: {},
      year: 0,
      headers: [
        { text: 'Date', value: 'Date', align: 'center' },
        { text: 'Libellé', value: 'Name', align: 'center' },
        { text: 'Montant', value: 'Value', align: 'center' },
        { text: 'RAM', value: 'Available', align: 'center' }
      ]
    }
  },
  methods: {
    newSearch(val) {
      this.search = val
      this.getDatas()
    },
    yearChanged(val) {
      if (val === null || val === '') {
        this.year = 0
        this.getDatas()
        return
      }
      if (/^20\d{2}$/.test(val)) {
        this.year = Number(val)
        this.getDatas()
      }
    },
    getDatas() {
      if (this.beneficiaryID) {
        this.$store.dispatch(types.GET_BENEFICIARY_DATAS, {
          ID: this.beneficiaryID,
          Search: this.search,
          Year: this.year,
          Page: this.pagination.page
        })
      }
    }
  },
  computed: {
    beneficiaryID() {
      return this.$route.params.ID
    },
    beneficiaryName() {
      return this.$route.params.Name
    },
    loading() {
      return this.$store.state.loading.loading !== 0
    },
    itemsCount() {
      return this.$store.state.beneficiary.beneficiaryItemsCount
    },
    pages() {
      if (this.pagination.rowsPerPage == null || this.itemsCount == null)
        return 0

      return Math.ceil(this.itemsCount / this.pagination.rowsPerPage)
    },
    items() {
      return this.$store.state.beneficiary.beneficiaryDatas
    }
  },
  mounted() {
    this.getDatas()
  }
}
</script>
