<template>
  <v-card>
    <v-card-title class="white--text primary headline"
      >Projets de renouvellement urbain</v-card-title
    >
    <v-card-text>
      <v-layout row wrap>
        <v-flex xs12 md6 offset-md3>
          <v-text-field label="Rechercher" v-model="search" />
        </v-flex>
        <v-flex xs12>
          <v-data-table
            :headers="headers"
            :items="items"
            item-key="ID"
            :loading="loading"
            :search="search"
            class="elevation-1"
          >
            <template slot="items" slot-scope="props">
              <td
                class="text-xs-left table-link"
                @click="selectItem(props.item.ID)"
              >
                {{ props.item.Reference }}
              </td>
              <td>
                <v-icon>
                  {{
                    props.item.PRIN ? 'check_box' : 'check_box_outline_blank'
                  }}
                </v-icon>
              </td>
              <td class="text-xs-left">{{ props.item.Cities }}</td>
              <td class="text-xs-left">{{ props.item.Name }}</td>
              <td class="text-xs-right">{{ props.item.Budget | currency }}</td>
              <td class="text-xs-right">
                {{ props.item.Population | number }}
              </td>
              <td class="text-xs-right">
                {{ props.item.CompositeIndex | nullable }}
              </td>
              <td class="pr-0 pl-0">
                <v-tooltip left v-if="hasRenewProjectRight">
                  <template v-slot:activator="{ on }">
                    <v-btn
                      color="primary"
                      small
                      flat
                      icon
                      class="pa-0"
                      @click="modify(props.item)"
                      v-on="on"
                    >
                      <v-icon>create</v-icon>
                    </v-btn>
                  </template>
                  Modifier
                </v-tooltip>
              </td>
              <td class="pr-0 pl-0">
                <v-tooltip left v-if="hasRenewProjectRight">
                  <template v-slot:activator="{ on }">
                    <v-btn
                      color="primary"
                      small
                      icon
                      flat
                      class="pa-0"
                      v-on="on"
                      @click="remove(props.item)"
                    >
                      <v-icon>delete</v-icon>
                    </v-btn>
                  </template>
                  Supprimer
                </v-tooltip>
              </td>
            </template>
          </v-data-table>
        </v-flex>
      </v-layout>
    </v-card-text>
    <v-divider />
    <v-card-actions v-if="hasRenewProjectRight">
      <v-spacer />
      <upload-button @fileChosen="upload($event)" />
      <v-btn color="primary" small flat @click="add">Créer</v-btn>
    </v-card-actions>

    <v-dialog v-model="dialog" persistent :overlay="false" max-width="500px">
      <v-card>
        <v-card-title class="primary white--text headline">
          {{ dlgTitle }}
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout row wrap>
              <v-flex xs6>
                <v-text-field
                  label="Référence"
                  v-model="item.Reference"
                  :rules="[refRule]"
                />
              </v-flex>
              <v-flex xs6>
                <v-checkbox label="PRIN" v-model="item.PRIN" />
              </v-flex>
              <v-flex>
                <v-autocomplete
                  v-model="item.CityCode1"
                  :items="cityItems"
                  :loading="loading"
                  hide-no-data
                  hide-selected
                  item-text="Name"
                  item-value="InseeCode"
                  label="Première ville"
                  required
                  :rules="[notNull]"
                />
              </v-flex>
              <v-flex>
                <v-autocomplete
                  v-model="item.CityCode2"
                  :items="cityItems"
                  :loading="loading"
                  hide-no-data
                  hide-selected
                  item-text="Name"
                  item-value="InseeCode"
                  label="Deuxième ville"
                />
              </v-flex>
              <v-flex>
                <v-autocomplete
                  v-model="item.CityCode3"
                  :items="cityItems"
                  :loading="loading"
                  hide-no-data
                  hide-selected
                  item-text="Name"
                  item-value="InseeCode"
                  label="Troisième ville"
                />
              </v-flex>
              <v-flex xs6>
                <v-text-field
                  label="Nom"
                  v-model="item.Name"
                  :rules="[notEmpty]"
                />
              </v-flex>
              <v-flex xs6>
                <v-text-field label="Budget" v-model="item.Budget" />
              </v-flex>
              <v-flex xs6>
                <v-text-field label="Population" v-model="item.Population" />
              </v-flex>
              <v-flex xs6>
                <v-text-field
                  label="Indice composite"
                  v-model="item.CompositeIndex"
                />
              </v-flex>
            </v-layout>
          </v-container>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" small flat @click="dialog = false"
            >Annuler</v-btn
          >
          <v-btn
            color="primary"
            small
            flat
            @click="save"
            :disabled="disabled"
            >{{ dlgBtn }}</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>

    <delete-dialog
      v-model="delDlg"
      sentence="Supprimer le projet définitivement ?"
      @confirm="delConfirm"
    />
  </v-card>
</template>

<script>
import * as types from '../store/types.js'
import UploadButton from './UploadButton.vue'
import DeleteDialog from './DeleteDialog'
const renewProjectRefCheck = r =>
  /^PR[RN](75|77|78|91|92|93|94|95)\d{2}$/.test(r)
export default {
  name: 'RenewProjects',
  components: { UploadButton, DeleteDialog },
  data() {
    return {
      search: '',
      headers: [
        { text: 'Référence', value: 'Reference' },
        { text: 'PRIN', value: 'PRIN' },
        { text: 'Communes', value: 'Cities' },
        { text: 'Nom', value: 'Name' },
        { text: 'Budget', value: 'Budget' },
        { text: 'Population', value: 'Population' },
        { text: 'Cat.', value: 'CompositeIndex' },
        { text: '', value: '', sortable: false, width: '1%' },
        { text: '', value: '', sortable: false, width: '1%' }
      ],
      dialog: false,
      item: {
        ID: null,
        Reference: '',
        Name: '',
        PRIN: false,
        CityCode1: null,
        CityCode2: null,
        CityCode3: null,
        Budget: null,
        Population: null,
        CompositeIndex: null
      },
      dlgTitle: 'Créer un projet de renouvellement',
      dlgBtn: 'Créer',
      delDlg: false,
      menu: false
    }
  },
  computed: {
    items() {
      return this.$store.state.renewProjects.renewProjectsList.map(p => ({
        ID: p.ID,
        PRIN: p.PRIN,
        Reference: p.Reference,
        Name: p.Name,
        Budget: p.Budget,
        CompositeIndex: p.CompositeIndex,
        CityCode1: p.CityCode1,
        CityCode2: p.CityCode2,
        CityCode3: p.CityCode3,
        Population: p.Population,
        Cities:
          p.CityName1 +
          (p.CityName2 ? ', ' + p.CityName2 : '') +
          (p.CityName3 ? ', ' + p.CityName3 : '')
      }))
    },
    cityItems() {
      return [
        { Name: '<Aucun>', InseeCode: null },
        ...this.$store.state.cities.citiesList
      ]
    },
    loading() {
      return this.$store.state.loading.loading !== 0
    },
    disabled() {
      return (
        !renewProjectRefCheck(this.item.Reference) ||
        this.item.Name === '' ||
        this.item.CityCode1 === null ||
        (this.item.Budget && !/^\d+[,.]?\d{0,2}$/.test(this.item.Budget)) ||
        (this.item.Population && !/^\d+$/.test(this.item.Budget)) ||
        (this.item.CompositeIndex &&
          !/^\d+[,.]?\d{0,2}$/.test(this.item.CompositeIndex))
      )
    },
    hasRenewProjectRight() {
      return this.$store.getters.hasRenewProjectRight
    }
  },
  methods: {
    add() {
      this.item = {
        ID: null,
        Reference: '',
        Name: '',
        PRIN: false,
        CityCode1: null,
        CityCode2: null,
        CityCode3: null,
        Budget: null,
        Population: null,
        CompositeIndex: null
      }
      this.dlgTitle = 'Créer un projet de renouvellement'
      this.dlgBtn = 'Créer'
      this.dialog = true
    },
    modify(item) {
      let i = this.items.find(i => i.ID === item.ID)
      this.item = {
        ID: i.ID,
        Reference: i.Reference,
        Name: i.Name,
        PRIN: i.PRIN,
        CityCode1: i.CityCode1,
        CityCode2: i.CityCode2,
        CityCode3: i.CityCode3,
        Budget: i.Budget / 100,
        Population: i.Population,
        CompositeIndex: i.CompositeIndex
      }
      this.dlgTitle = 'Modifier un projet de renouvellement'
      this.dlgBtn = 'Modifier'
      this.dialog = true
    },
    remove(item) {
      this.item.ID = item.ID
      this.delDlg = true
    },
    refRule(x) {
      return renewProjectRefCheck(x) || 'La référence doit être du type PRN9304'
    },
    notEmpty(x) {
      return x.length > 0 || 'Le nom doit être renseigné'
    },
    notNull(x) {
      return x !== null || 'Une ville doit être choisie'
    },
    selectItem(ID) {
      this.$router.push({ name: 'RenewProject', params: { ID } })
    },
    async delConfirm() {
      await this.$store.dispatch(types.DEL_RENEW_PROJECT, this.item.ID)
      this.delDlg = false
    },
    async save() {
      if (!this.disabled) {
        let dispatch =
          this.dlgBtn === 'Créer'
            ? types.CREATE_RENEW_PROJECT
            : types.UPDATE_RENEW_PROJECT
        this.$store.dispatch(dispatch, {
          ID: this.item.ID,
          Reference: this.item.Reference,
          Name: this.item.Name,
          PRIN: this.item.PRIN,
          CityCode1: this.item.CityCode1,
          CityCode2: this.item.CityCode2,
          CityCode3: this.item.CityCode3,
          Budget: this.item.Budget
            ? 100 * parseInt(String(this.item.Budget).replace(',', '.'))
            : null,
          Population: this.item.Population
            ? parseInt(this.item.Population)
            : null,
          CompositeIndex: this.item.CompositeIndex
            ? parseInt(String(this.item.CompositeIndex).replace(',', '.'))
            : null
        })
        this.dialog = false
      }
    },
    upload(file) {
      this.$store.dispatch(types.UPLOAD_RENEW_PROJECTS, file)
    }
  },
  created() {
    this.$store.dispatch(types.GET_RENEW_PROJECTS)
  }
}
</script>
