<template>
  <v-card>
    <v-card-title class="white--text primary headline"
      >Logement social</v-card-title
    >
    <v-tabs v-model="active" color="secondary" slider-color="primary">
      <v-tab ripple>Opérations</v-tab>
      <v-tab-item>
        <v-layout row wrap>
          <v-flex xs12 md6 offset-md3>
            <v-text-field
              label="Rechercher (référence, adresse, code INSEE)"
              v-model="opSearch"
              v-debounce:500ms="newSearch"
            />
          </v-flex>
          <v-flex xs12>
            <v-data-table
              :headers="headers"
              :items="items"
              item-key="ID"
              :loading="loading"
              :rows-per-page-items="[10]"
              :total-items="itemsCount"
              :pagination.sync="pagination"
              hide-actions
              class="elevation-1"
            >
              <template slot="items" slot-scope="props">
                <td class="text-xs-left table-link" @click="goTo(props.item)">
                  {{ props.item.Reference }}
                </td>
                <td class="text-xs-left table-link" @click="goTo(props.item)">
                  {{ props.item.Address }}
                </td>
                <td class="text-xs-left">{{ props.item.Housings }}</td>
                <td>
                  <v-icon>{{ props.item.ANRU ? 'done' : 'clear' }}</v-icon>
                </td>
                <td class="pr-0 pl-0">
                  <v-tooltip left v-if="hasHousingRight">
                    <template v-slot:activator="{ on }">
                      <v-btn
                        color="primary"
                        icon
                        small
                        flat
                        class="pa-0"
                        @click="modify(props.item)"
                      >
                        <v-icon class="pa-0" v-on="on">create</v-icon>
                      </v-btn>
                    </template>
                    <span>Modifier</span>
                  </v-tooltip>
                </td>
                <td class="pr-0 pl-0">
                  <v-tooltip left v-if="hasHousingRight">
                    <template v-slot:activator="{ on }">
                      <v-btn
                        color="primary"
                        icon
                        small
                        flat
                        class="pa-0"
                        @click="remove(props.item)"
                        v-on="on"
                      >
                        <v-icon class="pa-0" v-on="on">delete</v-icon>
                      </v-btn>
                    </template>
                    <span>Supprimer</span>
                  </v-tooltip>
                </td>
              </template>
            </v-data-table>
            <div class="text-xs-center pa-2">
              <v-pagination
                v-model="pagination.page"
                :length="pages"
                :total-visible="7"
                @input="getHousings"
              />
            </div>
          </v-flex>
          <v-flex xs12 v-if="hasHousingRight">
            <upload-button
              name="Import lien engagements"
              @fileChosen="commitentLinkUpload($event)"
            />
            <upload-button
              name="Import opérations"
              @fileChosen="operationUpload($event)"
            />
            <v-btn color="primary" small flat @click="add">Créer</v-btn>
          </v-flex>
        </v-layout>
      </v-tab-item>
      <v-tab>Prévisions</v-tab>
      <v-tab-item>
        <v-layout row wrap>
          <v-flex xs12 md6 offset-md3>
            <v-text-field
              label="Rechercher (référence, adresse, code INSEE)"
              v-model="forecastSearch"
            />
          </v-flex>
          <v-flex xs12>
            <v-data-table
              :headers="forecastHeaders"
              :items="housingForecasts"
              item-key="ID"
              :loading="loading"
              :search="forecastSearch"
              class="elevation-1"
            >
              <template slot="items" slot-scope="props">
                <td class="text-xs-left">
                  {{ props.item.CommissionDate | date }}
                </td>
                <td class="text-xs-left">{{ props.item.CommissionName }}</td>
                <td class="text-xs-left">{{ props.item.ActionName }}</td>
                <td class="text-xs-right">{{ props.item.Value | currency }}</td>
                <td class="pr-0 pl-0">
                  <v-tooltip left v-if="hasHousingRight">
                    <template v-slot:activator="{ on }">
                      <v-btn
                        color="primary"
                        icon
                        small
                        flat
                        class="pa-0"
                        @click="forecastModify(props.item)"
                      >
                        <v-icon class="pa-0" v-on="on">create</v-icon>
                      </v-btn>
                    </template>
                    <span>Modifier</span>
                  </v-tooltip>
                </td>
                <td class="pr-0 pl-0">
                  <v-tooltip left v-if="hasHousingRight">
                    <template v-slot:activator="{ on }">
                      <v-btn
                        color="primary"
                        icon
                        small
                        flat
                        class="pa-0"
                        @click="forecastRemove(props.item)"
                        v-on="on"
                      >
                        <v-icon class="pa-0" v-on="on">delete</v-icon>
                      </v-btn>
                    </template>
                    <span>Supprimer</span>
                  </v-tooltip>
                </td>
              </template>
            </v-data-table>
          </v-flex>
          <v-flex xs12 v-if="hasHousingRight">
            <v-btn color="primary" small flat @click="forecastAdd">Créer</v-btn>
          </v-flex>
        </v-layout>
      </v-tab-item>
    </v-tabs>

    <v-dialog v-model="dialog" persistent :overlay="false" max-width="500px">
      <v-card>
        <v-card-title class="primary white--text headline">{{
          dlgTitle
        }}</v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout row wrap>
              <v-flex xs12>
                <v-text-field label="Référence" v-model="item.Reference" />
              </v-flex>
              <v-flex xs12>
                <v-autocomplete
                  :items="cities"
                  item-text="Name"
                  item-value="InseeCode"
                  v-model="item.ZipCode"
                  label="Commune"
                />
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Adresse" v-model="item.Address" />
              </v-flex>
              <v-flex xs6>
                <v-text-field label="PLAI" v-model="item.PLAI" />
              </v-flex>
              <v-flex xs6>
                <v-text-field label="PLUS" v-model="item.PLUS" />
              </v-flex>
              <v-flex xs6>
                <v-text-field label="PLS" v-model="item.PLS" />
              </v-flex>
              <v-flex xs6>
                <v-checkbox label="ANRU" v-model="item.ANRU" />
              </v-flex>
            </v-layout>
          </v-container>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" small flat @click="dialog = false"
            >Annuler</v-btn
          >
          <v-btn
            color="primary"
            small
            flat
            @click="save"
            :disabled="disabled"
            >{{ dlgBtn }}</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog
      v-model="forecastDialog"
      persistent
      :overlay="false"
      max-width="500px"
    >
      <v-card>
        <v-card-title class="primary white--text headline">{{
          forecastDlgTitle
        }}</v-card-title>
        <v-card-text>
          <v-layout row wrap>
            <v-flex xs12>
              <v-autocomplete
                :items="commissions"
                item-text="Name"
                item-value="ID"
                v-model="forecastItem.CommissionID"
                label="Commission"
              />
            </v-flex>
            <v-flex xs12>
              <v-autocomplete
                :items="budgetActions"
                item-text="Name"
                item-value="ID"
                v-model="forecastItem.ActionID"
                label="Action budgétaire"
              />
            </v-flex>
            <v-flex xs12>
              <v-text-field label="Montant" v-model="forecastItem.Value" />
            </v-flex>
            <v-flex xs12>{{ formattedForecastValue }}</v-flex>
            <v-flex xs12>
              <v-textarea label="Commentaire" v-model="forecastItem.Comment" />
            </v-flex>
          </v-layout>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" small flat @click="forecastDialog = false"
            >Annuler</v-btn
          >
          <v-btn
            color="primary"
            small
            flat
            @click="forecastSave"
            :disabled="forecastDisabled"
            >{{ forecastDlgBtn }}</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>

    <delete-dialog
      v-model="delDlg"
      sentence="Supprimer le logement définitivement ?"
      @confirm="delConfirm"
    />

    <delete-dialog
      v-model="forecastDelDlg"
      sentence="Supprimer la prévision définitivement ?"
      @confirm="forecastDelConfirm"
    />
  </v-card>
</template>

<script>
import * as types from '../store/types.js'
import { nullable, currency } from '../filters.js'
import UploadButton from './UploadButton.vue'
import DeleteDialog from './DeleteDialog'
const checkValue = v => /^\d+([,.]\d{1,2})?$/.test(v)
export default {
  name: 'Housings',
  components: { UploadButton, DeleteDialog },
  data() {
    return {
      opSearch: '',
      forecastSearch: '',
      headers: [
        { text: 'Référence', value: 'Reference', sortable: false },
        { text: 'Adresse', value: 'Address', sortable: false },
        { text: 'Logements', value: 'Housings', sortable: false },
        { text: 'ANRU', value: 'ANRU', sortable: false },
        { text: '', value: '', sortable: false, width: '1%' },
        { text: '', value: '', sortable: false, width: '1%' }
      ],
      forecastHeaders: [
        { text: 'Date', value: 'CommissionDate' },
        { text: 'Commission', value: 'CommissionName' },
        { text: 'Action', value: 'ActionName' },
        { text: 'Montant', value: 'Value' },
        { text: '', value: '', sortable: false, width: '1%' },
        { text: '', value: '', sortable: false, width: '1%' }
      ],
      pagination: {},
      dialog: false,
      item: {
        Reference: '',
        ZipCode: null,
        PLAI: 0,
        PLUS: 0,
        PLS: 0,
        ANRU: false
      },
      forecastItem: {
        ID: null,
        CommissionID: null,
        CommissionDate: null,
        CommissionName: '',
        Value: 0,
        Comment: null,
        ActionID: null,
        ActionName: ''
      },
      dlgTitle: 'Créer un logement',
      dlgBtn: 'Créer',
      delDlg: false,
      forecastDialog: false,
      forecastDlgTitle: 'Créer une prévision',
      forecastDlgBtn: 'Créer',
      forecastDelDlg: false,
      active: null
    }
  },
  computed: {
    housings() {
      return this.$store.state.housings.housingsList
    },
    items() {
      return this.housings.map(i => ({
        ID: i.ID,
        Reference: i.Reference,
        Address: '[' + nullable(i.CityName) + '] ' + nullable(i.Address),
        Housings:
          'PLAI : ' + i.PLAI + ', PLUS : ' + i.PLUS + ', PLS : ' + i.PLS,
        ANRU: i.ANRU
      }))
    },
    loading() {
      return this.$store.state.loading.loading !== 0
    },
    disabled() {
      return this.item.Reference === ''
    },
    forecastDisabled() {
      return (
        this.forecastItem.CommissionID === null ||
        this.forecastItem.ActionID === null ||
        !checkValue(this.forecastItem.Value)
      )
    },
    formattedForecastValue() {
      return currency(
        parseInt(String(this.forecastItem.Value).replace(',', '.') * 100)
      )
    },
    itemsCount() {
      return this.$store.state.housings.housingsItemsCount
    },
    pages() {
      if (this.pagination.rowsPerPage == null || this.itemsCount == null)
        return 0

      return Math.ceil(this.itemsCount / this.pagination.rowsPerPage)
    },
    hasHousingRight() {
      return this.$store.getters.hasHousingRight
    },
    cities() {
      return this.$store.state.cities.citiesList
    },
    commissions() {
      return this.$store.state.settings.commissionsList
    },
    budgetActions() {
      return this.$store.state.settings.budgetActionsList
    },
    housingForecasts() {
      return this.$store.state.housings.housingForecastsList
    }
  },
  methods: {
    newSearch(val) {
      this.opSearch = val
      this.getHousings()
    },
    add() {
      this.item = {
        ID: null,
        Reference: '',
        Address: '',
        ZipCode: null,
        PLAI: 0,
        PLUS: 0,
        PLS: 0,
        ANRU: false
      }
      this.dlgTitle = 'Créer un logement'
      this.dlgBtn = 'Créer'
      this.dialog = true
    },
    modify(item) {
      let i = this.housings.find(i => i.ID === item.ID)
      this.item = {
        ID: i.ID,
        Reference: i.Reference,
        Address: i.Address,
        ZipCode: i.ZipCode,
        PLAI: i.PLAI,
        PLUS: i.PLUS,
        PLS: i.PLS,
        ANRU: i.ANRU
      }
      this.dlgTitle = 'Modifier un logement'
      this.dlgBtn = 'Modifier'
      this.dialog = true
    },
    remove(item) {
      this.item.ID = item.ID
      this.delDlg = true
    },
    forecastAdd() {
      this.forecastItem = {
        ID: null,
        CommissionID: null,
        CommissionDate: null,
        CommissionName: '',
        Value: 0,
        Comment: null,
        ActionID: null,
        ActionName: ''
      }
      this.forecastDlgTitle = 'Créer une prévision'
      this.forecastDlgBtn = 'Créer'
      this.forecastDialog = true
    },
    forecastModify(item) {
      let i = this.housingForecasts.find(i => i.ID === item.ID)
      this.forecastItem = { ...i }
      this.forecastItem.Value = i.Value / 100
      this.forecastDlgTitle = 'Modifier une précision'
      this.forecastDlgBtn = 'Modifier'
      this.forecastDialog = true
    },
    forecastRemove(item) {
      this.forecastItem = { ...item }
      this.forecastDelDlg = true
    },
    operationUpload(file) {
      this.$store.dispatch(types.UPLOAD_HOUSINGS, file)
    },
    commitentLinkUpload(file) {
      this.$store.dispatch(types.UPLOAD_HOUSING_COMMITMENT_LINK, file)
    },
    async delConfirm() {
      await this.$store.dispatch(types.DEL_HOUSING, this.item.ID)
      this.delDlg = false
    },
    async save() {
      if (!this.disabled) {
        let dispatch =
          this.dlgBtn === 'Créer' ? types.CREATE_HOUSING : types.UPDATE_HOUSING
        this.$store.dispatch(dispatch, {
          ID: this.item.ID,
          Reference: this.item.Reference,
          ZipCode: this.item.ZipCode ? Number(this.item.ZipCode) : null,
          Address: this.item.Address,
          ANRU: this.item.ANRU,
          PLAI: Number(this.item.PLAI),
          PLUS: Number(this.item.PLUS),
          PLS: Number(this.item.PLS)
        })
        this.dialog = false
      }
    },
    async getHousings(getCitiesList = false) {
      await this.$store.dispatch(types.GET_PAGINATED_HOUSINGS, {
        Search: this.opSearch,
        Page: this.pagination.page || 0,
        Lists: getCitiesList
      })
    },
    goTo({ ID }) {
      this.$router.push({ name: 'Housing', params: { ID } })
    },
    async forecastDelConfirm() {
      await this.$store.dispatch(
        types.DELETE_HOUSING_FORECAST,
        this.forecastItem
      )
      this.forecastDelDlg = false
    },
    async forecastSave() {
      if (!this.forecastDisabled) {
        let dispatch =
          this.forecastDlgBtn === 'Créer'
            ? types.CREATE_HOUSING_FORECAST
            : types.UPDATE_HOUSING_FORECAST
        let date = null
        if (this.forecastItem.Date) {
          let d = new Date(this.forecastItem.Date)
          date = d.toISOString()
        }
        this.$store.dispatch(dispatch, {
          ID: parseInt(this.forecastItem.ID),
          CommissionID: this.forecastItem.CommissionID,
          CommissionDate: date,
          CommissionName: this.forecastItem.CommissionName,
          Value: parseInt(
            String(this.forecastItem.Value).replace(',', '.') * 100
          ),
          Comment: this.forecastItem.Comment,
          ActionID: parseInt(this.forecastItem.ActionID),
          ActionName: this.forecastItem.ActionName
        })
        this.forecastDialog = false
      }
    }
  },
  created() {
    this.getHousings(true)
  }
}
</script>
