<template>
  <v-app>
    <v-snackbar bottom color="error" v-model="showErrorMsg">
      {{ errorMsg }}
    </v-snackbar>

    <v-navigation-drawer fixed v-model="drawer" app v-if="user">
      <v-list>
        <v-list-tile
          v-for="item in menuItems"
          :key="item.id"
          :to="item.routerLink"
        >
          <v-list-tile-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>{{ item.title }}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>

    <v-toolbar color="primary" dark fixed app v-if="user">
      <v-toolbar-side-icon @click.stop="drawer = !drawer" />
      <v-toolbar-title>
        PreLoRU
        <span class="body-1"
          >{{ user ? user.Name : '' }} {{ isAdmin ? '[adm]' : '' }}</span
        >
      </v-toolbar-title>
      <v-spacer />
      <v-toolbar-items>
        <v-btn flat @click="onLogout" v-if="user">Déconnexion</v-btn>
      </v-toolbar-items>
    </v-toolbar>

    <v-content>
      <v-container fluid>
        <v-layout align-center wrap>
          <v-flex xs12 v-if="loading">
            <v-progress-circular indeterminate class="primary--text" />
          </v-flex>
          <v-flex xs12 v-if="user !== null">
            <router-view />
          </v-flex>
          <v-dialog v-model="dialog" width="500" persistent>
            <v-card>
              <v-card-title class="title primary white--text"
                >PreLoRU - Connexion</v-card-title
              >
              <v-card-text>
                <v-layout row wrap>
                  <v-flex xs12>
                    <v-text-field
                      v-model="email"
                      label="Email"
                      required
                      :rules="[notEmpty]"
                    />
                  </v-flex>
                  <v-flex xs12>
                    <v-text-field
                      v-model="password"
                      label="Mot de passe"
                      :append-icon="pwdShow ? 'visibility' : 'visibility_off'"
                      :type="pwdShow ? 'text' : 'password'"
                      @click:append="pwdShow = !pwdShow"
                      @keyup.enter="onConnectDlg"
                      required
                      :rules="[notEmpty]"
                    />
                  </v-flex>
                </v-layout>
              </v-card-text>
              <v-divider />
              <v-card-actions>
                <v-spacer />
                <v-btn
                  color="primary"
                  flat
                  @click="onConnectDlg"
                  :disabled="disabled"
                  >Connexion</v-btn
                >
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import * as types from './store/types.js'
export default {
  name: 'App',
  data: () => ({
    drawer: false,
    showErrorMsg: false,
    email: '',
    password: '',
    dialog: true,
    pwdShow: false
  }),
  computed: {
    user() {
      return this.$store.state.token.user
    },
    isAdmin() {
      return this.$store.getters.isAdmin
    },
    loading() {
      return this.$store.getters.loading
    },
    menuItems() {
      return this.$store.getters.menuItems
    },
    errorMsg() {
      return this.$store.state.loading.errorMsg
    },
    disabled() {
      return this.email === '' || this.password === ''
    }
  },
  created() {
    this.$store.commit(types.RETRIEVE_TOKEN)
  },
  methods: {
    async onConnectDlg() {
      if (!this.disabled) {
        await this.$store.dispatch(types.LOG_IN, {
          email: this.email,
          password: this.password
        })
      }
    },
    onLogout() {
      this.$store.dispatch(types.LOG_OUT)
      this.email = ''
      this.password = ''
    },
    notEmpty(text) {
      return text.length > 0 || 'À remplir'
    }
  },
  watch: {
    errorMsg(msg) {
      if (msg) this.showErrorMsg = true
    },
    user() {
      this.dialog = this.user === null
    }
  }
}
</script>

<style>
.table-link {
  text-decoration-line: none;
  cursor: pointer;
}
.table-link:hover {
  text-decoration-line: underline;
  color: #4a148c;
}
</style>
