import Vue from 'vue'
import Vuex from 'vuex'

// Modules
import token from './store/token'
import loading from './store/loading'
import users from './store/users'
import housings from './store/housings'
import copros from './store/copros'
import renewProjects from './store/renew_projects'
import settings from './store/settings'
import cities from './store/cities'
import communities from './store/communities'
import commitments from './store/commitments'
import payments from './store/payments'
import beneficiaries from './store/beneficiaries'
import home from './store/home'
import beneficiary from './store/beneficiary'
import ratios from './store/ratios'
import pmtForecasts from './store/pmtForecasts'
import renewProject from './store/renew_project'
import copro from './store/copro'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    loading,
    token,
    users,
    housings,
    copros,
    renewProjects,
    settings,
    cities,
    communities,
    commitments,
    payments,
    beneficiaries,
    home,
    beneficiary,
    ratios,
    pmtForecasts,
    renewProject,
    copro
  }
})
