<template>
  <v-dialog
    :value="value"
    @input="$emit('input', $event.target.value)"
    persistent
    max-width="500px"
  >
    <v-card>
      <v-card-title class="primary white--text headline"
        >Confirmer la suppression</v-card-title
      >
      <v-card-text>{{ sentence }}</v-card-text>
      <v-divider></v-divider>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="primary" small flat @click="$emit('input', false)"
          >Annuler</v-btn
        >
        <v-btn color="primary" small flat @click="$emit('confirm')"
          >Supprimer</v-btn
        >
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: 'DeleteDialog',
  props: {
    sentence: { type: String, required: true },
    value: { type: Boolean, required: true }
  }
}
</script>
