import * as types from './types.js'
import Vue from 'vue'
import { beginLoading, setErrorMessage } from './loading.js'

const state = {
  currentYearCommitments: [],
  currentYearPayments: [],
  previousYearCommitments: [],
  previousYearPayments: []
}

const actions = {
  async [types.GET_HOME_DATAS]({ commit }) {
    beginLoading(commit)
    try {
      let resp = await Vue.http.get('home')
      commit(types.GET_HOME_DATAS, resp.body)
      commit(types.END_LOADING)
    } catch (err) {
      setErrorMessage(commit, err)
    }
  }
}

function normMonthValues(datas) {
  let ret = []
  for (let i = 0, m = 1, cur = 0; i < datas.length; m++) {
    if (datas[i].Month === m) {
      cur = datas[i].Value
      i++
    }
    ret.push(cur)
  }
  return ret
}

const mutations = {
  [types.GET_HOME_DATAS](state, payload) {
    state.currentYearCommitments = [
      ...normMonthValues(payload.Commitment.CurrentYear)
    ]
    state.previousYearCommitments = [
      ...normMonthValues(payload.Commitment.PreviousYear)
    ]
    state.currentYearPayments = [
      ...normMonthValues(payload.Payment.CurrentYear)
    ]
    state.previousYearPayments = [
      ...normMonthValues(payload.Payment.PreviousYear)
    ]
  }
}

export default { state, actions, mutations }
