<template>
  <v-card>
    <v-card-title class="white--text primary headline">Accueil</v-card-title>
    <v-card-text>
      <v-layout row wrap>
        <v-flex xs12 md6 pa-2>
          <div class="title pb-2">Engagements</div>
          <g-chart
            :data="commitments"
            :options="chartOptions"
            type="LineChart"
            :settings="{ packages: ['corechart'], language: 'fr' }"
          />
        </v-flex>
        <v-flex xs12 md6 pa-2>
          <div class="title pb-2">Paiements</div>
          <g-chart
            :data="payments"
            :options="chartOptions"
            type="LineChart"
            :settings="{ packages: ['corechart'], language: 'fr' }"
          />
        </v-flex>
      </v-layout>
    </v-card-text>
  </v-card>
</template>

<script>
import * as types from '../store/types.js'
import { GChart } from 'vue-google-charts'
const month = [
  'Jan',
  'Fev',
  'Mar',
  'Avr',
  'Mai',
  'Jun',
  'Jul',
  'Aoû',
  'Sep',
  'Oct',
  'Nov',
  'Dec'
]
const year = new Date().getFullYear()
export default {
  name: 'Home',
  components: { GChart },
  data() {
    return {
      chartOptions: {
        fontName: 'Roboto',
        vAxis: { format: 'short' },
        height: 500,
        legend: { position: 'none' },
        chartArea: { left: 50, top: 50, width: '100%', height: 400 },
        series: {
          0: { color: '#CE93D8', lineWidth: 4 },
          1: { color: '#4A148C', lineWidth: 4 }
        }
      }
    }
  },
  computed: {
    currentCommitments() {
      return this.$store.state.home.currentYearCommitments
    },
    currentPayments() {
      return this.$store.state.home.currentYearPayments
    },
    previousCommitments() {
      return this.$store.state.home.previousYearCommitments
    },
    previousPayments() {
      return this.$store.state.home.previousYearPayments
    },
    commitments() {
      return this.getChartDatas(
        this.previousCommitments,
        this.currentCommitments
      )
    },
    payments() {
      return this.getChartDatas(this.previousPayments, this.currentPayments)
    }
  },
  methods: {
    getChartDatas(s1, s2) {
      let datas = [
        [
          { label: 'Mois', type: 'string' },
          { label: String(year - 1), type: 'number' },
          { label: String(year), type: 'number' }
        ]
      ]
      let l1 = s1.length
      let l2 = s2.length
      for (let i = 0; i < 12; i++) {
        datas.push([month[i], i < l1 ? s1[i] : null, i < l2 ? s2[i] : null])
      }
      return datas
    }
  },
  created() {
    this.$store.dispatch(types.GET_HOME_DATAS)
  }
}
</script>
